import React, { Fragment, useEffect, useState } from "react";
import { useParams, useNavigate } from 'react-router-dom';
import clienteAxios from "../../config/axios"; // Configuración de Axios para hacer solicitudes HTTP
import Swal from 'sweetalert2'; // Librería para mostrar alertas

// Componente principal para editar un proyecto existente
function EditarProyecto() {
    // Extrae el ID del proyecto desde los parámetros de la URL
    const { id_proyecto } = useParams(); 
    const navigate = useNavigate(); // Hook para la navegación después de actualizar
    const [proyecto, guardaProyectos] = useState({
        nombre: '',
        descripcion: '',
        fecha_inicio: '',
        fecha_fin_estimada: '',
        estado: '',
        recursos: '',
        presupuesto_estimado: ''
    });

    // Función para consultar el proyecto en la API usando su ID
    const consultarAPI = async () => {
        try {
            const proyectoConsulta = await clienteAxios.get(`/proyecto/${id_proyecto}`);
            const proyectoData = proyectoConsulta.data;

            // Formatea las fechas al formato "yyyy-MM-dd"
            const fecha_inicio = new Date(proyectoData.fecha_inicio).toISOString().split("T")[0];
            const fecha_fin_estimada = new Date(proyectoData.fecha_fin_estimada).toISOString().split("T")[0];

            // Actualiza el estado con las fechas formateadas
            guardaProyectos({
                ...proyectoData,
                fecha_inicio,
                fecha_fin_estimada
            });
        } catch (error) {
            console.error("Error al obtener el proyecto:", error);
            Swal.fire("Error", "El proyecto no se pudo encontrar o no existe", "error"); // Mensaje más claro para el usuario
        }
    };

    // useEffect para ejecutar la consulta al cargar el componente
    useEffect(() => {
        consultarAPI();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [id_proyecto]); // La dependencia es el ID del proyecto

    // Actualiza el estado del proyecto en base a los datos del formulario
    const actualizarState = e => {
        guardaProyectos({
            ...proyecto,
            [e.target.name]: e.target.value || '' // Asignar un valor vacío si es undefined o null
        });
    };

    // Limpia los campos del formulario
    const limpiarFormulario = () => {
        guardaProyectos({
            nombre: '',
            descripcion: '',
            fecha_inicio: '',
            fecha_fin_estimada: '',
            estado: '',
            recursos: '',
            presupuesto_estimado: ''
        });
    };

    // Valida si todos los campos están llenos
    const validarProyecto = () => {
        const { nombre, descripcion, fecha_inicio, fecha_fin_estimada, estado, recursos, presupuesto_estimado } = proyecto;
        // Retorna true si algún campo está vacío
        return !nombre || !descripcion || !fecha_inicio || !fecha_fin_estimada || !estado || !recursos || !presupuesto_estimado;
    };

    // Función para enviar la solicitud de actualización a la API
    const agregarProyecto = async (e) => {
        e.preventDefault(); // Previene el comportamiento por defecto del formulario

        try {
            // Realiza una solicitud PUT para actualizar el proyecto en la API sin almacenar el resultado
            await clienteAxios.put(`/proyecto/${id_proyecto}`, proyecto);
            
            // Muestra un mensaje de éxito
            Swal.fire("Se Actualizó el Proyecto", `Código del proyecto actualizado: ${id_proyecto}`, "success");
            navigate('/clientes'); // Redirige a la página principal después de la actualización
        } catch (error) {
            console.error("Error al actualizar el proyecto:", error); // Muestra el error en consola si falla
            Swal.fire("Error", "No se pudo actualizar el proyecto", "error"); // Muestra una alerta de error
        }
    };

    return (
        <Fragment>
            <h2>Editar Proyecto</h2>
            <form onSubmit={agregarProyecto}>
                <legend>Llena todos los campos</legend>

                {/* Campos del formulario */}
                <div className="campo">
                    <label>Nombre del Proyecto:</label>
                    <input type="text" placeholder="Nombre del Proyecto" name="nombre" value={proyecto.nombre} onChange={actualizarState} />
                </div>
                <div className="campo">
                    <label>Descripción:</label>
                    <input type="text" placeholder="Descripción" name="descripcion" value={proyecto.descripcion} onChange={actualizarState} />
                </div>
                <div className="campo">
                    <label htmlFor="fecha_inicio">Fecha de Inicio:</label>
                    <input type="date" name="fecha_inicio" id="fecha_inicio" value={proyecto.fecha_inicio} onChange={actualizarState} required />
                </div>
                <div className="campo">
                    <label htmlFor="fecha_fin_estimada">Fecha Fin Estimada:</label>
                    <input type="date" name="fecha_fin_estimada" id="fecha_fin_estimada" value={proyecto.fecha_fin_estimada} onChange={actualizarState} required />
                </div>
                <div className="campo">
                    <label htmlFor="estado">Estado:</label>
                    <select name="estado" id="estado" value={proyecto.estado} required onChange={actualizarState}>
                        <option value="">Seleccione un estado</option>
                        <option value="A">Activo</option>
                        <option value="P">Progreso</option>
                        <option value="F">Finalizado</option>
                        <option value="C">Cancelado</option>
                    </select>
                </div>
                <div className="campo">
                    <label>Recursos:</label>
                    <input type="text" placeholder="Recursos" name="recursos" value={proyecto.recursos} onChange={actualizarState} />
                </div>
                <div className="campo">
                    <label>Presupuesto Estimado:</label>
                    <input type="text" placeholder="Presupuesto Estimado" name="presupuesto_estimado" value={proyecto.presupuesto_estimado} onChange={actualizarState} />
                </div>

                {/* Contenedor de botones */}
                <div style={{ display: 'flex', justifyContent: 'flex-start', marginTop: '20px' }}>
                    {/* Botón para actualizar el proyecto */}
                    <input type="submit" className="btn btn-azul" value="Actualizar Proyecto" disabled={validarProyecto()} style={{ padding: '10px 20px', marginRight: '10px' }} />
                    {/* Botón para limpiar el formulario */}
                    <button type="button" onClick={limpiarFormulario} style={{ padding: '10px 20px', backgroundColor: '#dc3545', color: 'white', border: 'none', borderRadius: '5px', cursor: 'pointer' }}>
                        Limpiar Campos
                    </button>
                </div>
            </form>
        </Fragment>
    );
}

// Exporta el componente para usarlo en otras partes de la aplicación
export default EditarProyecto;
