import React from "react";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import clienteAxios from "../../config/axios";

function DefectoPrueba({ proyecto, onDelete }) {
    const {
        id_defecto,nombre,descripcion,
        fecha_registro,id_proyecto,prioridad,
        responsable,estado,fecha_actualizacion,evidencia
    } = proyecto;

    // Función para eliminar proyecto
    const eliminarProyecto = (id_defecto) => {
        Swal.fire({
            title: "¿Estás seguro?",
            text: "Se eliminará el registro",
            icon: "warning",
            showCancelButton: true,
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            confirmButtonText: "Sí, eliminar",
            cancelButtonText: "Cancelar",
        }).then((result) => {
            if (result.isConfirmed) {
                // Llamada a Axios para eliminar
                clienteAxios.delete(`/defecto/${id_defecto}`)
                    .then((res) => {
                        console.log(res);
                        onDelete(); // Llama a onDelete para recargar la lista
                        Swal.fire("Eliminado!", "El defecto ha sido eliminado.", "success");
                    })
                    .catch((error) => {
                        console.error("Error al eliminar el proyecto:", error);
                        Swal.fire("Error", "No se pudo eliminar el defecto", "error");
                    });
            }
        });
    };

    const formatDate = (dateString) => {
        if (!dateString) return '';
        const options = { year: 'numeric', month: '2-digit', day: '2-digit' };
        return new Date(dateString).toLocaleDateString('es-GT', options);
    };

    return (
        <li className="cliente">
            <div className="info-cliente">
                <p className="nombre"><strong>Código defecto:</strong> {id_defecto}</p>
                <p className="empresa"><strong>Nombre:</strong> {nombre}</p>
                <p><strong>Descripción:</strong> {descripcion}</p>
                <p><strong>Fecha de Creación:</strong> {formatDate(fecha_registro)}</p>
                <p><strong>Código Proyecto:</strong> {id_proyecto}</p>
                <p><strong>Prioridad:</strong> {prioridad}</p>
                <p><strong>Responsable:</strong> {responsable}</p>
                <p><strong>Estado:</strong> {estado}</p>
                <p><strong>Fecha de Actualización:</strong> {formatDate(fecha_actualizacion)}</p>
                <p><strong>Evidencia:</strong> {evidencia}</p>
            </div>
            <div className="acciones">
                {/* Asegúrate de que estas rutas estén correctamente configuradas en tu sistema de rutas */}
                <Link to={"/defectos/nuevo"} className="btn btn-verde nvo-cliente">
                    <i className="fas fa-plus-circle"></i> Nuevo Defecto
                </Link>
                <Link to={`/defectos/editar/${id_defecto}`} className="btn btn-azul">
                    <i className="fas fa-pen-alt"></i> Editar Defecto
                </Link>
                <button
                    type="button"
                    className="btn btn-rojo btn-eliminar"
                    onClick={() => eliminarProyecto(id_defecto)}
                >
                    <i className="fas fa-times"></i> Eliminar Defecto
                </button>
            </div>
        </li>
    );
}

export default DefectoPrueba;
