import React, { Fragment, useState, useEffect } from "react";
import clienteAxios from "../../config/axios";
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';

// Componente principal que permite agregar un nuevo caso de prueba
function NuevoCasoPrueba() {
    const navigate = useNavigate();
    const [proyecto, guardaProyectos] = useState({
        id_escenario: '',
        descripcion: '',
        criterios_aceptacion: '',
    });

    const [escenarios, setEscenarios] = useState([]); // Estado para almacenar los escenarios obtenidos

    // Efecto para cargar los escenarios cuando el componente se monta
    useEffect(() => {
        clienteAxios.get('/escenario') // Se asume que la ruta '/escenario' devuelve los escenarios
            .then(res => {
                console.log(res.data); // Para verificar la estructura de los datos
                setEscenarios(res.data); // Guardar los escenarios en el estado
            })
            .catch(error => {
                console.error("Error al obtener escenarios:", error.response?.data || error.message);
            });
    }, []);

    // Actualizar el estado del proyecto al cambiar los campos del formulario
    const actualizarState = e => {
        guardaProyectos({
            ...proyecto,
            [e.target.name]: e.target.value
        });
    };

    // Enviar el formulario para agregar un nuevo caso de prueba
    const agregarProyecto = e => {
        e.preventDefault();
        clienteAxios.post('/caso', proyecto, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(res => {
            Swal.fire(
                "Se Agregó el Caso de Prueba",
                `Código del nuevo caso: ${res.data.id}`,
                "success"
            );
            navigate('/casos');
        })
        .catch(error => {
            console.error("Error al agregar el caso:", error.response?.data || error.message);
        });
    };

    // Limpiar los campos del formulario
    const limpiarFormulario = () => {
        guardaProyectos({
            id_escenario: '',
            descripcion: '',
            criterios_aceptacion: ''
        });
    };

    // Validar si falta algún campo requerido para el formulario
    const validarProyecto = () => {
        const { id_escenario, criterios_aceptacion, descripcion } = proyecto;
        return !id_escenario || !criterios_aceptacion || !descripcion;
    };

    return (
        <Fragment>
            <h2>Nuevo Caso de Prueba</h2>
            <form onSubmit={agregarProyecto}>
                <legend>Llena todos los campos</legend>

                {/* Campo de selección para el escenario */}
                <div className="campo">
                    <label htmlFor="id_escenario">Código Escenario:</label>
                    <select
                        name="id_escenario"
                        id="id_escenario"
                        value={proyecto.id_escenario}
                        onChange={actualizarState}
                        required
                    >
                        <option value="">Seleccione un escenario</option>
                        {escenarios.map(esc => (
                            <option key={esc.id_escenario} value={esc.id_escenario}>
                                {`ID: ${esc.id_escenario} - ${esc.nombre}`}
                            </option>
                        ))}
                    </select>
                </div>

                {/* Campos del formulario */}
                <div className="campo">
                    <label>Descripción:</label>
                    <input
                        type="text"
                        placeholder="Descripción"
                        name="descripcion"
                        value={proyecto.descripcion}
                        onChange={actualizarState}
                    />
                </div>
                <div className="campo">
                    <label>Criterios de Aceptación:</label>
                    <input
                        type="text"
                        placeholder="Criterios de Aceptación"
                        name="criterios_aceptacion"
                        value={proyecto.criterios_aceptacion}
                        onChange={actualizarState}
                    />
                </div>

                {/* Contenedor para los botones con estilos en línea */}
                <div style={{ display: 'flex', justifyContent: 'flex-start', marginTop: '20px' }}>
                    <input
                        type="submit"
                        className="btn btn-azul"
                        value="Agregar Caso de Prueba"
                        disabled={validarProyecto()}
                        style={{ padding: '10px 20px', marginRight: '10px' }}
                    />
                    <button
                        type="button"
                        onClick={limpiarFormulario}
                        style={{
                            padding: '10px 20px',
                            backgroundColor: '#dc3545',
                            color: 'white',
                            border: 'none',
                            borderRadius: '5px',
                            cursor: 'pointer'
                        }}
                    >
                        Limpiar Campos
                    </button>
                </div>
            </form>
        </Fragment>
    );
}

// Exportar el componente
export default NuevoCasoPrueba;
