import React, { Fragment, useEffect, useState } from "react";
import { useParams, useNavigate } from 'react-router-dom';
import clienteAxios from "../../config/axios"; // Configuración de Axios para hacer solicitudes HTTP
import Swal from 'sweetalert2'; // Librería para mostrar alertas

// Componente principal para editar un proyecto existente
function EditarEscenario() {
    // Extrae el ID del proyecto desde los parámetros de la URL
    const { id_escenario } = useParams(); 
    const navigate = useNavigate(); // Hook para la navegación después de actualizar
    const [proyecto, guardaProyectos] = useState({
        id_plan_prueba: '',
        nombre: '',
        descripcion: ''
    });

    // Función para consultar el proyecto en la API usando su ID
    const consultarAPI = async () => {
        try {
            const proyectoConsulta = await clienteAxios.get(`/escenario/${id_escenario}`);
            const proyectoData = proyectoConsulta.data;

            // Actualiza el estado con las fechas formateadas
            guardaProyectos({
                ...proyectoData
            });
        } catch (error) {
            console.error("Error al obtener el proyecto:", error);
            Swal.fire("Error", "El proyecto no se pudo encontrar o no existe", "error"); // Mensaje más claro para el usuario
        }
    };

    // useEffect para ejecutar la consulta al cargar el componente
    useEffect(() => {
        consultarAPI();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [id_escenario]); // La dependencia es el ID del proyecto

    // Actualiza el estado del proyecto en base a los datos del formulario
    const actualizarState = e => {
        guardaProyectos({
            ...proyecto,
            [e.target.name]: e.target.value // Cambia solo el valor del campo modificado
        });
    };

    // Limpia los campos del formulario
    const limpiarFormulario = () => {
        guardaProyectos({
            id_plan_prueba: '',
            nombre: '',
            descripcion: '',
        });
    };

    // Valida si todos los campos están llenos
    const validarProyecto = () => {
        const { id_plan_prueba, nombre, descripcion } = proyecto;
        // Retorna true si algún campo está vacío
        return !id_plan_prueba || !nombre || !descripcion;
    };

  
// Función para enviar la solicitud de actualización a la API
const agregarProyecto = async (e) => {
    e.preventDefault(); // Previene el comportamiento por defecto del formulario

    try {
        // Realiza una solicitud PUT para actualizar el proyecto en la API sin almacenar el resultado
        await clienteAxios.put(`/escenario/${id_escenario}`, proyecto);
        
        // Muestra un mensaje de éxito
        Swal.fire("Se Actualizó el Proyecto", `Código del proyecto actualizado: ${id_escenario}`, "success");
        navigate('/escenarios'); // Redirige a la página principal después de la actualización
    } catch (error) {
        console.error("Error al actualizar el proyecto:", error); // Muestra el error en consola si falla
        Swal.fire("Error", "No se pudo actualizar el proyecto", "error"); // Muestra una alerta de error
    }
};


    return (
        <Fragment>
            <h2>Editar Escenario</h2>
            <form onSubmit={agregarProyecto}>
                <legend>Llena todos los campos</legend>

                {/* Campos del formulario */}
                <div className="campo">
                    <label>Código Plan:</label>
                    <input type="text" placeholder="Código Plan" name="id_plan_prueba" value={proyecto.id_plan_prueba} onChange={actualizarState} disabled />
                </div>
                <div className="campo">
                    <label>Nombre del Escenario:</label>
                    <input type="text" placeholder="Nombre del Escenario" name="nombre" value={proyecto.nombre} onChange={actualizarState} />
                </div>
                <div className="campo">
                    <label>Descripción:</label>
                    <input type="text" placeholder="Descripción" name="descripcion" value={proyecto.descripcion} onChange={actualizarState} />
                </div>


                {/* Contenedor de botones */}
                <div style={{ display: 'flex', justifyContent: 'flex-start', marginTop: '20px' }}>
                    {/* Botón para actualizar el proyecto */}
                    <input type="submit" className="btn btn-azul" value="Actualizar Escenario" disabled={validarProyecto()} style={{ padding: '10px 20px', marginRight: '10px' }} />
                    {/* Botón para limpiar el formulario */}
                    <button type="button" onClick={limpiarFormulario} style={{ padding: '10px 20px', backgroundColor: '#dc3545', color: 'white', border: 'none', borderRadius: '5px', cursor: 'pointer' }}>
                        Limpiar Campos
                    </button>
                </div>
            </form>
        </Fragment>
    );
}

// Exporta el componente para usarlo en otras partes de la aplicación
export default EditarEscenario;
