import React, { Fragment, useEffect, useState } from "react";
import { useParams, useNavigate } from 'react-router-dom';
import clienteAxios from "../../config/axios"; // Configuración de Axios para hacer solicitudes HTTP
import Swal from 'sweetalert2'; // Librería para mostrar alertas

// Componente principal para editar un proyecto existente
function EditarPlan() {
    // Extrae el ID del proyecto desde los parámetros de la URL
    const { id_plan } = useParams(); 
    const navigate = useNavigate(); // Hook para la navegación después de actualizar
    const [proyecto, guardaProyectos] = useState({
        nombre: '',
        descripcion: '',
        fecha_ejecucion: '',
        estado: ''
    });

    // Función para consultar el proyecto en la API usando su ID
    const consultarAPI = async () => {
        try {
            const proyectoConsulta = await clienteAxios.get(`/plan/${id_plan}`);
            const proyectoData = proyectoConsulta.data;

            // Formatea las fechas al formato "yyyy-MM-dd"
            const fecha_ejecucion = new Date(proyectoData.fecha_ejecucion).toISOString().split("T")[0];
            //const fecha_fin_estimada = new Date(proyectoData.fecha_fin_estimada).toISOString().split("T")[0];

            // Actualiza el estado con las fechas formateadas
            guardaProyectos({
                ...proyectoData,
                fecha_ejecucion,
                //fecha_fin_estimada
            });
        } catch (error) {
            console.error("Error al obtener el plan:", error);
            Swal.fire("Error", "El plan no se pudo encontrar o no existe", "error"); // Mensaje más claro para el usuario
        }
    };

    // useEffect para ejecutar la consulta al cargar el componente
    useEffect(() => {
        consultarAPI();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [id_plan]); // La dependencia es el ID del proyecto

    // Actualiza el estado del proyecto en base a los datos del formulario
    const actualizarState = e => {
        guardaProyectos({
            ...proyecto,
            [e.target.name]: e.target.value || '' // Asignar un valor vacío si es undefined o null
        });
    };

    // Limpia los campos del formulario
    const limpiarFormulario = () => {
        guardaProyectos({
            nombre: '',
            descripcion: '',
            fecha_ejecucion: '',
            estado: ''
        });
    };

    // Valida si todos los campos están llenos
    const validarProyecto = () => {
        const { nombre, descripcion, fecha_ejecucion,  estado} = proyecto;
        // Retorna true si algún campo está vacío
        return !nombre || !descripcion || !fecha_ejecucion || !estado;
    };

    // Función para enviar la solicitud de actualización a la API
    const agregarProyecto = async (e) => {
        e.preventDefault(); // Previene el comportamiento por defecto del formulario

        try {
            // Realiza una solicitud PUT para actualizar el proyecto en la API sin almacenar el resultado
            await clienteAxios.put(`/plan/${id_plan}`, proyecto);
            
            // Muestra un mensaje de éxito
            Swal.fire("Se Actualizó el Plan", `Código del plan actualizado: ${id_plan}`, "success");
            navigate('/productos'); // Redirige a la página principal después de la actualización
        } catch (error) {
            console.error("Error al actualizar el plan:", error); // Muestra el error en consola si falla
            Swal.fire("Error", "No se pudo actualizar el plan", "error"); // Muestra una alerta de error
        }
    };

    return (
        <Fragment>
            <h2>Editar Plan</h2>
            <form onSubmit={agregarProyecto}>
                <legend>Llena todos los campos</legend>

                {/* Campos del formulario */}
                <div className="campo">
                    <label>Nombre del Plan:</label>
                    <input type="text" placeholder="Nombre del Plan" name="nombre" value={proyecto.nombre} onChange={actualizarState} />
                </div>
                <div className="campo">
                    <label>Descripción:</label>
                    <input type="text" placeholder="Descripción" name="descripcion" value={proyecto.descripcion} onChange={actualizarState} />
                </div>
                <div className="campo">
                    <label htmlFor="fecha_ejecucion">Fecha de Ejecucion:</label>
                    <input type="date" name="fecha_ejecucion" id="fecha_ejecucion" value={proyecto.fecha_ejecucion} onChange={actualizarState} required />
                </div>
        
                <div className="campo">
                    <label htmlFor="estado">Estado:</label>
                    <select name="estado" id="estado" value={proyecto.estado} required onChange={actualizarState}>
                        <option value="">Seleccione un estado</option>
                        <option value="activo">Activo</option>
                        <option value="proceso">Progreso</option>
                        <option value="finalizado">Finalizado</option>
                        <option value="cancelado">Cancelado</option>
                    </select>
                </div>
               

                {/* Contenedor de botones */}
                <div style={{ display: 'flex', justifyContent: 'flex-start', marginTop: '20px' }}>
                    {/* Botón para actualizar el proyecto */}
                    <input type="submit" className="btn btn-azul" value="Actualizar Plan" disabled={validarProyecto()} style={{ padding: '10px 20px', marginRight: '10px' }} />
                    {/* Botón para limpiar el formulario */}
                    <button type="button" onClick={limpiarFormulario} style={{ padding: '10px 20px', backgroundColor: '#dc3545', color: 'white', border: 'none', borderRadius: '5px', cursor: 'pointer' }}>
                        Limpiar Campos
                    </button>
                </div>
            </form>
        </Fragment>
    );
}

// Exporta el componente para usarlo en otras partes de la aplicación
export default EditarPlan;
