import React, { Fragment, useEffect, useState } from "react";
import { useParams, useNavigate } from 'react-router-dom';
import clienteAxios from "../../config/axios"; // Configuración de Axios para hacer solicitudes HTTP
import Swal from 'sweetalert2'; // Librería para mostrar alertas

// Componente principal para editar un proyecto existente
function EditarUsuario() {
    // Extrae el ID del proyecto desde los parámetros de la URL
    const { idUsuario } = useParams(); 
    const navigate = useNavigate(); // Hook para la navegación después de actualizar
    const [proyecto, guardaProyectos] = useState({
      nombre: '', apellido: '',password:'',usuario: '', correo: '',rol: '',estado: ''
    });

    // Función para consultar el proyecto en la API usando su ID
    const consultarAPI = async () => {
        try {
            const proyectoConsulta = await clienteAxios.get(`/usuarios/${idUsuario}`);
            const proyectoData = proyectoConsulta.data;

            // Actualiza el estado con las fechas formateadas
            guardaProyectos({
                ...proyectoData
            });
        } catch (error) {
            console.error("Error al obtener el usuario:", error);
            Swal.fire("Error", "El usuario no se pudo encontrar o no existe", "error"); // Mensaje más claro para el usuario
        }
    };

    // useEffect para ejecutar la consulta al cargar el componente
    useEffect(() => {
        consultarAPI();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [idUsuario]); // La dependencia es el ID del proyecto

    // Actualiza el estado del proyecto en base a los datos del formulario
    const actualizarState = e => {
        guardaProyectos({
            ...proyecto,
            [e.target.name]: e.target.value // Cambia solo el valor del campo modificado
        });
    };

    // Limpia los campos del formulario
    const limpiarFormulario = () => {
        guardaProyectos({
         nombre: '', apellido: '',password:'',usuario: '', correo: '',rol: '',estado: ''
        });
    };

    // Valida si todos los campos están llenos
    const validarProyecto = () => {
        const {nombre, apellido,password,usuario, correo,rol,estado} = proyecto;
        return !nombre|| !apellido||!password||!usuario || !correo|| !rol|| !estado;
    };

  
// Función para enviar la solicitud de actualización a la API
const agregarProyecto = async (e) => {
    e.preventDefault(); // Previene el comportamiento por defecto del formulario

    try {
        // Realiza una solicitud PUT para actualizar el proyecto en la API sin almacenar el resultado
        await clienteAxios.put(`/usuarios/${idUsuario}`, proyecto);
        
        // Muestra un mensaje de éxito
        Swal.fire("Se Actualizó el Usuario", `Código del usuario actualizado: ${idUsuario}`, "success");
        navigate('/usuarios'); // Redirige a la página principal después de la actualización
    } catch (error) {
        console.error("Error al actualizar el usuario:", error); // Muestra el error en consola si falla
        Swal.fire("Error", "No se pudo actualizar el usuario", "error"); // Muestra una alerta de error
    }
};


    return (
        <Fragment>
            <h2>Editar Usuario</h2>
            <form onSubmit={agregarProyecto}>
                <legend>Llena todos los campos</legend>

                {/* Campos del formulario */}
                <div className="campo">
                    <label>Nombre:</label>
                    <input type="text" placeholder="Ingrese nombre" name="nombre" value={proyecto.nombre} onChange={actualizarState} />
                </div>
                <div className="campo">
                    <label>Apellido:</label>
                    <input type="text" placeholder="Ingrese apellido" name="apellido" value={proyecto.apellido} onChange={actualizarState} />
                </div>
                <div className="campo">
                    <label>Usuario:</label>
                    <input type="text" placeholder="Ingrese usuario" name="usuario" value={proyecto.usuario} onChange={actualizarState} disabled/>
                </div>
                <div className="campo">
                    <label>Password:</label>
                    <input type="password" placeholder="Ingrese password" name="password" value={proyecto.password} onChange={actualizarState} 
                    onFocus={() => guardaProyectos({ ...proyecto, password: '' })} />
                </div>
                <div className="campo">
                    <label>Correo Eléctronico:</label>
                    <input type="text" placeholder="Ingrese correo" name="correo" value={proyecto.correo} onChange={actualizarState} disabled/>
                </div>
                <div className="campo">
                    <label htmlFor="rol">Rol:</label>
                    <select name="rol" id="rol" value={proyecto.rol} required onChange={actualizarState}>
                        <option value="">Seleccione un Rol</option>
                        <option value="admin">Admin</option>
                        <option value="test">Test</option>
                        <option value="desarrollador">Desarrollador</option>
                    </select>
                </div>
                <div className="campo">
                    <label htmlFor="estado">Estado:</label>
                    <select name="estado" id="estado" value={proyecto.estado} required onChange={actualizarState}>
                        <option value="">Seleccione un estado</option>
                        <option value="A">Activo</option>
                        <option value="I">Inactivo</option>
                    </select>
                </div>

                {/* Contenedor de botones */}
                <div style={{ display: 'flex', justifyContent: 'flex-start', marginTop: '20px' }}>
                    {/* Botón para actualizar el proyecto */}
                    <input type="submit" className="btn btn-azul" value="Actualizar Usuario" disabled={validarProyecto()} style={{ padding: '10px 20px', marginRight: '10px' }} />
                    {/* Botón para limpiar el formulario */}
                    <button type="button" onClick={limpiarFormulario} style={{ padding: '10px 20px', backgroundColor: '#dc3545', color: 'white', border: 'none', borderRadius: '5px', cursor: 'pointer' }}>
                        Limpiar Campos
                    </button>
                </div>
            </form>
        </Fragment>
    );
}

// Exporta el componente para usarlo en otras partes de la aplicación
export default EditarUsuario;
