import React from "react";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import clienteAxios from "../../config/axios";

function Usuario({ proyecto, onDelete }) {
    const {
      idUsuario,nombre, apellido,usuario, correo,rol, fechaCreacion,
      fechaActualizacion, estado
    } = proyecto;

    // Función para eliminar proyecto
    const eliminarProyecto = (idUsuario) => {
        Swal.fire({
            title: "¿Estás seguro?",
            text: "Se eliminará el registro",
            icon: "warning",
            showCancelButton: true,
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            confirmButtonText: "Sí, eliminar",
            cancelButtonText: "Cancelar",
        }).then((result) => {
            if (result.isConfirmed) {
                // Llamada a Axios para eliminar
                clienteAxios.delete(`/usuarios/${idUsuario}`)
                    .then((res) => {
                        console.log(res);
                        onDelete(); // Llama a onDelete para recargar la lista
                        Swal.fire("Eliminado!", "El usuario ha sido eliminado.", "success");
                    })
                    .catch((error) => {
                        console.error("Error al eliminar el usuario:", error);
                        Swal.fire("Error", "No se pudo eliminar el Usuario", "error");
                    });
            }
        });
    };

    const formatDate = (dateString) => {
        if (!dateString) return '';
        const options = { year: 'numeric', month: '2-digit', day: '2-digit' };
        return new Date(dateString).toLocaleDateString('es-GT', options);
    };
    return (
        <li className="cliente">
            <div className="info-cliente">
                <p className="nombre"><strong>Código Usuario:</strong> {idUsuario}</p>
                <p className="empresa"><strong>Nombre completo:</strong> {`${nombre} ${apellido}` }</p>
                <p><strong>Usuario:</strong> {usuario}</p>
                <p><strong>Correo:</strong> {correo}</p>
                <p><strong>Rol:</strong> {rol}</p>
                <p><strong>Fecha de Creación:</strong> {formatDate(fechaCreacion)}</p>
                <p><strong>Última Actualización:</strong> {formatDate(fechaActualizacion)}</p>
                <p><strong>Estado:</strong> {estado}</p>
            </div>
            <div className="acciones">
                {/* Asegúrate de que estas rutas estén correctamente configuradas en tu sistema de rutas */}
                <Link to={"/usuarios/nuevo"} className="btn btn-verde nvo-cliente">
                    <i className="fas fa-plus-circle"></i> Nuevo Usuario
                </Link>
                <Link to={`/usuarios/editar/${idUsuario}`} className="btn btn-azul">
                    <i className="fas fa-pen-alt"></i> Editar Usuario
                </Link>
                <button
                    type="button"
                    className="btn btn-rojo btn-eliminar"
                    onClick={() => eliminarProyecto(idUsuario)}
                >
                    <i className="fas fa-times"></i> Eliminar Usuario
                </button>
            </div>
        </li>
    );
}

export default Usuario;
