import React from "react";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import clienteAxios from "../../config/axios";

function Escenario({ proyecto, onDelete }) {
    const {
        id_escenario, id_plan_prueba,
        nombre, descripcion
    } = proyecto;

    // Función para eliminar proyecto
    const eliminarProyecto = (idProyecto) => {
        Swal.fire({
            title: "¿Estás seguro?",
            text: "Se eliminará el registro",
            icon: "warning",
            showCancelButton: true,
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            confirmButtonText: "Sí, eliminar",
            cancelButtonText: "Cancelar",
        }).then((result) => {
            if (result.isConfirmed) {
                // Llamada a Axios para eliminar
                clienteAxios.delete(`/escenario/${idProyecto}`)
                    .then((res) => {
                        console.log(res);
                        onDelete(); // Llama a onDelete para recargar la lista
                        Swal.fire("Eliminado!", "El proyecto ha sido eliminado.", "success");
                    })
                    .catch((error) => {
                        console.error("Error al eliminar el proyecto:", error);
                        Swal.fire("Error", "No se pudo eliminar el proyecto", "error");
                    });
            }
        });
    };

    return (
        <li className="cliente">
            <div className="info-cliente">
                <p className="nombre"><strong>Código Escenario:</strong> {id_escenario}</p>
                <p><strong>Código Plan:</strong> {id_plan_prueba}</p>
                <p className="empresa"><strong>Nombre:</strong> {nombre}</p>
                <p><strong>Descripción:</strong> {descripcion}</p>
            </div>
            <div className="acciones">
                {/* Asegúrate de que estas rutas estén correctamente configuradas en tu sistema de rutas */}
                <Link to={"/escenarios/nuevo"} className="btn btn-verde nvo-cliente">
                    <i className="fas fa-plus-circle"></i> Nuevo Escenario
                </Link>
                <Link to={`/escenarios/editar/${id_escenario}`} className="btn btn-azul">
                    <i className="fas fa-pen-alt"></i> Editar Escenario
                </Link>
                <button
                    type="button"
                    className="btn btn-rojo btn-eliminar"
                    onClick={() => eliminarProyecto(id_escenario)}
                >
                    <i className="fas fa-times"></i> Eliminar escenario
                </button>
            </div>
        </li>
    );
}

export default Escenario;
