import React from "react";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import clienteAxios from "../../config/axios";

function Proyecto({ proyecto, onDelete }) {
    const {
        id_proyecto,
        nombre,
        descripcion,
        fecha_inicio,
        fecha_fin_estimada,
        estado,
        recursos,
        presupuesto_estimado,
        fecha_creacion,
        ultima_actualizacion,
    } = proyecto;

    // Eliminar proyecto
    const eliminarProyecto = (idProyecto) => {
        Swal.fire({
            title: "¿Estás seguro?",
            text: "Se eliminará el registro",
            icon: "warning",
            showCancelButton: true,
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            confirmButtonText: "Sí, eliminar",
            cancelButtonText: "Cancelar",
        }).then((result) => {
            if (result.isConfirmed) {
                // Llamado a axios para eliminar
                clienteAxios.delete(`/proyecto/${idProyecto}`)
                    .then((res) => {
                        console.log(res);
                        // Llamar a onDelete para recargar la lista de proyectos
                        onDelete();
                        Swal.fire("Eliminado!", "El proyecto ha sido eliminado.", "success");
                    })
                    .catch((error) => {
                        console.error("Error al eliminar el proyecto:", error);
                        Swal.fire("Error", "No se pudo eliminar el proyecto", "error");
                    });
            }
        });
    };

    const formatDate = (dateString) => {
        if (!dateString) return '';
        const options = { year: 'numeric', month: '2-digit', day: '2-digit' };
        return new Date(dateString).toLocaleDateString('es-GT', options);
    };

    // Función para mapear el valor corto del estado a su texto completo
    const obtenerEstadoTexto = (estado) => {
        switch (estado) {
            case "A":
                return "Activo";
            case "P":
                return "Progreso";
            case "F":
                return "Finalizado";
            case "C":
                return "Cancelado";
            default:
                return "Estado desconocido";
        }
    };
    
    return (
        <li className="cliente">
            <div className="info-cliente">
                <p className="nombre"><strong>Código Proyecto:</strong> {id_proyecto}</p>
                <p className="empresa"><strong>Nombre:</strong> {nombre}</p>
                <p><strong>Descripción:</strong> {descripcion}</p>
                <p><strong>Fecha de Inicio:</strong> {formatDate(fecha_inicio)}</p>
                <p><strong>Fecha Fin Estimada:</strong> {formatDate(fecha_fin_estimada)}</p>
                <p><strong>Estado:</strong> {obtenerEstadoTexto(estado)}</p> {/* Aquí se aplica la función */}
                <p><strong>Recursos:</strong> {recursos}</p>
                <p><strong>Presupuesto Estimado:</strong> {presupuesto_estimado}</p>
                <p><strong>Fecha de Creación:</strong> {formatDate(fecha_creacion)}</p>
                <p><strong>Última Actualización:</strong> {formatDate(ultima_actualizacion)}</p>
            </div>
            <div className="acciones">
                <Link to={`/proyecto/editar/${id_proyecto}`} className="btn btn-azul">
                    <i className="fas fa-pen-alt"></i> Editar Proyecto
                </Link>
                <button
                    type="button"
                    className="btn btn-rojo btn-eliminar"
                    onClick={() => eliminarProyecto(id_proyecto)}
                >
                    <i className="fas fa-times"></i> Eliminar Proyecto
                </button>
            </div>
        </li>
    );
}

export default Proyecto;
