import React, { useContext, useState } from "react";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
import clienteAxios from "../../config/axios";
import { CRMContext } from "../../context/CRMContext";

function Login() {
    // Context para manejar el estado global de autenticación
    const [, guardarAuth] = useContext(CRMContext);

    // Hook para redirigir a otras rutas
    const navigate = useNavigate();

    // Estado para almacenar credenciales ingresadas por el usuario
    const [credenciales, guardarCredenciales] = useState({
        usuario: "",
        password: "",
    });

    // Estado para indicar si la solicitud está en progreso (spinner)
    const [loading, setLoading] = useState(false);

    /**
     * Maneja el envío del formulario para iniciar sesión.
     * Realiza una solicitud al backend y maneja los posibles errores.
     */
    const iniciarSesion = async (e) => {
        e.preventDefault(); // Evita el comportamiento por defecto del formulario

        // Validación básica para campos vacíos
        if (!credenciales.usuario || !credenciales.password) {
            Swal.fire({
                icon: "warning",
                title: "Campos incompletos",
                text: "Por favor, completa todos los campos.",
            });
            return;
        }

        // Muestra el spinner mientras se realiza la solicitud
        setLoading(true);

        try {
            // Llamada al backend para autenticar al usuario
            const respuesta = await clienteAxios.post("/usuario/iniciar-sesion", credenciales);

            // Muestra un mensaje de éxito
            Swal.fire({
                icon: "success",
                title: "Inicio de sesión exitoso",
                text: "Bienvenido de nuevo.",
            });

            // Extrae el token de la respuesta y lo guarda en el localStorage
            const { token, rol } = respuesta.data;
            localStorage.setItem("token", token);
            localStorage.setItem("rol", rol);

            // Actualiza el estado global de autenticación
            guardarAuth({
                token,
                auth: true,
                rol,
            });

            // Redirige a la página principal
            navigate("/clientes");
        } catch (error) {
            // Manejo de errores con mensaje personalizado
            Swal.fire({
                icon: "error",
                title: "Error de autenticación",
                text: error.response?.data?.mensaje || "Error desconocido. Intenta nuevamente.",
            });
        } finally {
            // Oculta el spinner independientemente del resultado
            setLoading(false);
        }
    };

    /**
     * Actualiza el estado de las credenciales mientras el usuario escribe.
     */
    const leerDatos = (e) => {
        guardarCredenciales({
            ...credenciales,
            [e.target.name]: e.target.value,
        });
    };

    /**
     * Limpia los campos del formulario.
     */
    const limpiarCampos = () => {
        guardarCredenciales({
            usuario: "",
            password: "",
        });
    };

    return (
        <div className="login">
            {/* Logo de la aplicación */}
            <div className="imagen-login" style={{ display: "flex", justifyContent: "center", margin: "20px 0" }}>
                <img
                    src="/LogoUMG.png"
                    alt="Logo UMG"
                    style={{ maxWidth: "30%", height: "auto" }}
                />
            </div>

            {/* Título del formulario */}
            <h2 style={{ textAlign: "center", marginBottom: "20px" }}>Iniciar Sesión</h2>

            {/* Contenedor del formulario */}
            <div className="contenedor-formulario">
                <form onSubmit={iniciarSesion}>
                    {/* Campo de Usuario */}
                    <div className="campo">
                        <label htmlFor="usuario">Usuario</label>
                        <input
                            id="usuario"
                            type="text"
                            name="usuario"
                            placeholder="Ingrese su usuario"
                            value={credenciales.usuario}
                            onChange={leerDatos}
                            className="input"
                            required
                        />
                    </div>

                    {/* Campo de Contraseña */}
                    <div className="campo">
                        <label htmlFor="password">Contraseña</label>
                        <input
                            id="password"
                            type="password"
                            name="password"
                            placeholder="Ingrese su contraseña"
                            value={credenciales.password}
                            onChange={leerDatos}
                            className="input"
                            required
                        />
                    </div>

                    {/* Botones del formulario */}
                    <div className="botones">
                        {/* Botón para iniciar sesión */}
                        <button
                            type="submit"
                            className="btn btn-verde btn-block"
                            style={{
                                backgroundColor: "#28a745",
                                color: "#fff",
                                padding: "10px 20px",
                                fontSize: "16px",
                                border: "none",
                                borderRadius: "5px",
                                cursor: "pointer",
                                margin: "10px 0",
                            }}
                            disabled={loading}
                        >
                            {loading ? "Cargando..." : "Ingresar"}
                        </button>

                        {/* Botón para limpiar campos */}
                        <button
                            type="button"
                            className="btn btn-rojo btn-block"
                            style={{
                                backgroundColor: "#dc3545",
                                color: "#fff",
                                padding: "10px 20px",
                                fontSize: "16px",
                                border: "none",
                                borderRadius: "5px",
                                cursor: "pointer",
                                margin: "10px 0",
                            }}
                            onClick={limpiarCampos}
                            disabled={loading}
                        >
                            Limpiar Campos
                        </button>
                    </div>
                </form>
            </div>
        </div>
    );
}

export default Login;
