import React, { Fragment, useContext, useEffect, useState } from "react";
import {
  BrowserRouter as Router,
  Route,
  Routes,
  Navigate,
} from "react-router-dom";

// Importar componentes de layout
import Header from "./componentes/layout/Header";
import Navegacion from "./componentes/layout/Navegacion";

// Importar componentes principales
import Proyectos from "./componentes/proyecto/Proyectos";
import EditarPlan from "./componentes/producto/EditarPlan";
import Planes from "./componentes/producto/Planes";
import NuevoProyecto from "./componentes/proyecto/NuevoProyecto";
import EditarProyecto from "./componentes/proyecto/EditarProyecto";
import NuevoPlan from "./componentes/producto/NuevoPlan";

// Importar componentes de Escenarios de Prueba
import Escenarios from "./componentes/escenarioPrueba/Escenarios";
import NuevoEscenario from "./componentes/escenarioPrueba/NuevoEscenario";
import EditarEscenario from "./componentes/escenarioPrueba/EditarEscenario";

// Importar componentes de Casos de Prueba
import CasosPrueba from "./componentes/casoPrueba/CasosPrueba";
import NuevoCasoPrueba from "./componentes/casoPrueba/NuevoCasoPrueba";
import EditarCaso from "./componentes/casoPrueba/EditarCasoPrueba";

// Importar componentes de usuarios
import Usuarios from "./componentes/usuario/usuarios";
import NuevoUsuario from "./componentes/usuario/NuevoUsuario";
import EditarUsuario from "./componentes/usuario/EditarUsuario";

// Importar componentes de defectos
import DefectosPrueba from "./componentes/defecto/DefectosPrueba";
import NuevoDefecto from "./componentes/defecto/NuevoDefecto";
import EditarDefecto from "./componentes/defecto/EditarDefecto";

// Importar componente de autenticación
import Login from "./componentes/auth/Login";

// Contexto de autenticación
import { CRMContext, CRMProvider } from "./context/CRMContext";
import NuevaPrueba from "./componentes/ejecucionPrueba/NuevaPrueba";
import Informe from "./componentes/informes/Informe";

/**
 * Componente para proteger rutas
 * Verifica si el usuario está autenticado antes de permitir el acceso
 */
const RutaProtegida = ({ children }) => {
  const [auth] = useContext(CRMContext);

  // Si no está autenticado, redirigir al login
  if (!auth.token) {
    return <Navigate to="/usuario" replace />;
  }

  return children; // Renderizar contenido protegido si está autenticado
};

const AdminPage = ({ children }) => {
  const [auth] = useContext(CRMContext);
  const [showMessage, setShowMessage] = useState(false);
  const [redirect, setRedirect] = useState(false);

  useEffect(() => {
    if (auth.rol !== "admin") {
      setShowMessage(true); // Mostrar el mensaje
      setTimeout(() => {
        setRedirect(true); // Ejecutar el redireccionamiento después de un tiempo
      }, 3000); // Esperar 3 segundos (o el tiempo que desees)
    }
  }, [auth.rol]);

  if (redirect) {
    return <Navigate to="/clientes" replace />;
  }

  if (showMessage) {
    return <div>No tienes permisos para acceder a esta página. Redirigiendo...</div>;
  }

  return children;
};


function App() {
  const [auth, guardarAuth] = useContext(CRMContext);

  return (
    <Router>
      <Fragment>
        <CRMProvider value={[auth, guardarAuth]}>
          {/* Encabezado principal */}
          <Header />
          <div className="grid contenedor contenido-principal">
            {/* Barra de navegación */}
            <Navegacion />
            <main className="caja-contenido col-9">
              <Routes>
                {/* Rutas protegidas */}
                <Route
                  path="/clientes"
                  element={
                    <RutaProtegida>
                      <Proyectos />
                    </RutaProtegida>
                  }
                />
                <Route
                  path="/proyecto/nuevo"
                  element={
                    <RutaProtegida>
                      <NuevoProyecto />
                    </RutaProtegida>
                  }
                />
                <Route
                  path="/proyecto/editar/:id_proyecto"
                  element={
                    <RutaProtegida>
                      <EditarProyecto />
                    </RutaProtegida>
                  }
                />
                <Route
                  path="/productos"
                  element={
                    <RutaProtegida>
                      <Planes />
                    </RutaProtegida>
                  }
                />
                <Route
                  path="/productos/nuevo"
                  element={
                    <RutaProtegida>
                      <NuevoPlan />
                    </RutaProtegida>
                  }
                />
                <Route
                  path="/productos/editar/:id_plan"
                  element={
                    <RutaProtegida>
                      <EditarPlan />
                    </RutaProtegida>
                  }
                />
                <Route
                  path="/escenarios"
                  element={
                    <RutaProtegida>
                      <Escenarios />
                    </RutaProtegida>
                  }
                />
                <Route
                  path="/escenarios/nuevo"
                  element={
                    <RutaProtegida>
                      <NuevoEscenario />
                    </RutaProtegida>
                  }
                />
                <Route
                  path="/escenarios/editar/:id_escenario"
                  element={
                    <RutaProtegida>
                      <EditarEscenario />
                    </RutaProtegida>
                  }
                />
                <Route
                  path="/casos"
                  element={
                    <RutaProtegida>
                      <CasosPrueba />
                    </RutaProtegida>
                  }
                />
                <Route
                  path="/casos/nuevo"
                  element={
                    <RutaProtegida>
                      <NuevoCasoPrueba />
                    </RutaProtegida>
                  }
                />
                <Route
                  path="/casos/editar/:id_caso"
                  element={
                    <RutaProtegida>
                      <EditarCaso />
                    </RutaProtegida>
                  }
                />
                <Route
                  path="/usuarios"
                  element={
                    <AdminPage>
                      <Usuarios />
                    </AdminPage>
                  }
                />
                <Route
                  path="/usuarios/nuevo"
                  element={
                    <AdminPage>
                      <NuevoUsuario />
                    </AdminPage>
                  }
                />
                <Route
                  path="/usuarios/editar/:idUsuario"
                  element={
                    <RutaProtegida>
                      <EditarUsuario />
                    </RutaProtegida>
                  }
                />
                <Route
                  path="/defectos"
                  element={
                    <RutaProtegida>
                      <DefectosPrueba />
                    </RutaProtegida>
                  }
                />
                <Route
                  path="/defectos/nuevo"
                  element={
                    <RutaProtegida>
                      <NuevoDefecto />
                    </RutaProtegida>
                  }
                />
                <Route
                  path="/defectos/editar/:id_defecto"
                  element={
                    <RutaProtegida>
                      <EditarDefecto />
                    </RutaProtegida>
                  }
                />
                {/* Ruta para hacer las pruebas */}
                <Route
                  path="/prueba"
                  element={
                    <RutaProtegida>
                      <NuevaPrueba />
                    </RutaProtegida>
                  }
                />

                {/* Ruta para los informes */}
                <Route
                  path="/informes"
                  element={
                    <RutaProtegida>
                      <Informe />
                    </RutaProtegida>
                  }
                />

                {/* Ruta pública para inicio de sesión */}
                <Route path="/usuario" element={<Login />} />

                {/* Redirigir cualquier otra ruta al login */}
                <Route path="*" element={<Navigate to="/usuario" replace />} />
              </Routes>
            </main>
          </div>
        </CRMProvider>
      </Fragment>
    </Router>
  );
}

export default App;
