import React, { useState, useEffect } from "react";

const CRMContext = React.createContext([{}, () => {}]);

const CRMProvider = (props) => {
    const [auth, guardarAuth] = useState({
        token: localStorage.getItem("token") || "",
        auth: !!localStorage.getItem("token"), // Si hay un token, está autenticado
        rol: localStorage.getItem("rol") || "", // Almacena el rol del usuario
    });

    // Sincronizar con localStorage
    useEffect(() => {
        if (auth.token) {
            localStorage.setItem("token", auth.token);
            localStorage.setItem("rol", auth.rol); // Guardar rol
        } else {
            localStorage.removeItem("token");
            localStorage.removeItem("rol");
        }
    }, [auth]);

    return (
        <CRMContext.Provider value={[auth, guardarAuth]}>
            {props.children}
        </CRMContext.Provider>
    );
};

export { CRMContext, CRMProvider };
