import React, { useContext } from "react";
import { Link } from "react-router-dom";
import { CRMContext } from "../../context/CRMContext";

const Navegacion = () => {
  const [auth] = useContext(CRMContext);
  if (!auth.auth) return null;

  return (
    <aside className="sidebar col-3">
      <h2>Módulos</h2>

      <nav className="navegacion">
        <Link to={"/clientes"} className="clientes">
          Gestión de proyectos
        </Link>
        <Link to={"/productos"} className="productos">
          Planificación de pruebas
        </Link>
        <Link to={"/prueba"} className="ejecucion">
          Ejecución de pruebas
        </Link>
        <Link to={"/defectos"} className="defectos">
          Gestión de defectos
        </Link>
        <Link to={"/informes"} className="informes">
          Informes y métricas
        </Link>

        {/* Mostrar en caso de que el usuario sea administrador */}
        {auth.rol === "admin" && (
          <Link to={"/usuarios"} className="usuarios">
            Gestión de usuarios
          </Link>
        )}
      </nav>
    </aside>
  );
};

export default Navegacion;
