// src/componentes/producto/Planes.js

import React, { useEffect, useState, Fragment } from "react";
import { Link } from "react-router-dom"; // Importar Link desde react-router-dom
import clienteAxios from "../../config/axios";
import Plan from "./Plan";
import Swal from "sweetalert2";

function Planes() {
    // Estado para almacenar la lista de proyectos obtenidos de la API
    const [proyectos, guardaProyectos] = useState([]);
    // Estado para controlar si los datos se están cargando
    const [loading, setLoading] = useState(true);
    // Estado para controlar la recarga de los datos cuando se elimina un proyecto
    const [reload, setReload] = useState(false);
    // Estado para el término de búsqueda
    const [searchTerm, setSearchTerm] = useState('');

    // Función que consulta la API para obtener la lista de proyectos
    const consultarAPI = async () => {
        setLoading(true); // Inicia el estado de carga
        try {
            const proyectosConsulta = await clienteAxios.get('/plan');
            if (proyectosConsulta.data && Array.isArray(proyectosConsulta.data)) {
                // Guarda los datos obtenidos de la API en el estado
                guardaProyectos(proyectosConsulta.data);
            } else {
                console.error("La respuesta de la API no es válida:", proyectosConsulta.data);
            }
        } catch (error) {
            console.error("Error al obtener los proyectos:", error);
        } finally {
            setLoading(false); // Finaliza el estado de carga
        }
    };

    // Ejecuta la consulta a la API cuando el componente se monta o cuando `reload` cambia
    useEffect(() => {
        consultarAPI();
    }, [reload]); // Solo se actualiza cuando `reload` cambia

    // Función para manejar la eliminación de un proyecto y forzar la recarga de la lista
    const handleDelete = () => {
        setReload(!reload); // Alterna `reload` para forzar la actualización
    };

    // Función para manejar el cambio en el campo de búsqueda
    const handleSearchChange = (e) => {
        const value = e.target.value; // Obtiene el valor del input
        setSearchTerm(value); // Actualiza el término de búsqueda en el estado

        // Verifica si el proyecto existe con base en el término de búsqueda
        const foundProject = proyectos.find(proyecto => proyecto.id_plan.toString() === value);
        if (value && !foundProject) {
            Swal.fire({
                icon: 'error',
                title: 'No existe el registro',
                text: `No se encontró un proyecto con el ID: ${value}`,
            });
        }
    };

    // Filtra los proyectos con base en el término de búsqueda
    const filteredProyectos = proyectos.filter(proyecto =>
        proyecto.id_plan.toString().toLowerCase().includes(searchTerm.toLowerCase())
    );

    return (
        <Fragment>
            {/* Contenedor para los botones con estilos específicos */}
            <div style={{ display: 'flex', gap: '10px', marginBottom: '20px' }}>
                <Link to={"/escenarios"} className="btn btn-verde btn-escenario">
                    <i className="fas fa-plus-circle"></i> Escenario Prueba
                </Link>
                <Link to={"/casos"} className="btn btn-azul btn-caso">
                    <i className="fas fa-plus-circle"></i> Caso Prueba
                </Link>
            </div>
            <hr/>
            <h2>Planes de Proyecto</h2>
            <hr/>
            {/* Campo de búsqueda que se muestra una vez en el componente */}
            <div className="search-container">
                <i className="fas fa-search search-icon"></i> {/* Ícono de búsqueda */}
                <input
                    type="text"
                    placeholder="Buscar plan por código"
                    value={searchTerm}
                    onChange={handleSearchChange}
                    className="search-input"
                />
            </div>

            {/* Muestra mensaje de carga mientras se obtienen los datos */}
            {loading ? (
                <p>Cargando proyectos...</p>
            ) : (
                <ul className="listado-clientes">
                    {/* Mapea la lista de proyectos filtrados y los renderiza */}
                    {filteredProyectos.map(proyecto => (
                        <Plan
                            key={proyecto.id_plan}
                            proyecto={proyecto}
                            onDelete={handleDelete} // Pasa la función de recarga al componente hijo
                        />
                    ))}
                </ul>
            )}
        </Fragment>
    );
}

export default Planes; // Asegúrate de exportarlo por defecto
