import React, { useContext } from "react";
import { useNavigate } from "react-router-dom"; // Para manejar la navegación
import { CRMContext } from "../../context/CRMContext"; // Importar el contexto

const Header = () => {
    const [auth, guardarAuth] = useContext(CRMContext); // Obtenemos el estado y el método para actualizarlo
    const navigate = useNavigate(); // Hook para redirigir al login

    /**
     * Maneja el cierre de sesión del usuario.
     * Limpia el token del localStorage y actualiza el contexto.
     */
    const cerrarSesion = () => {
        // Elimina el token del localStorage
        localStorage.removeItem("token");

        // Actualiza el estado de autenticación
        guardarAuth({
            token: null,
            auth: false,
        });

        // Redirige al usuario a la página de Login
        navigate("/usuario");
    };

    return (
        <header className="barra">
            <div className="contenedor">
                <div className="contenido-barra">
                    <h1>Sistema Gestión De Pruebas y Control De Software</h1>

                    {/* Condicional para mostrar el botón de cerrar sesión */}
                    {auth?.auth && (
                        <button
                            type="button"
                            className="btn btn-rojo"
                            onClick={cerrarSesion} // Ejecuta la función de cierre de sesión al hacer clic
                        >
                            <i className="far fa-times-circle"></i>
                            Cerrar Sesión
                        </button>
                    )}
                </div>
            </div>
        </header>
    );
};

export default Header;
