import React, { Fragment, useEffect, useState } from "react";
import { jsPDF } from "jspdf";
import clienteAxios from "../../config/axios";

function Informe() {
  const [historial, setHistorial] = useState([]);
  const [expandedItems, setExpandedItems] = useState({});
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(true); // Estado de carga

  useEffect(() => {
    const obtenerHistorial = async () => {
      try {
        const response = await clienteAxios.get("/ejecucion/historial");
        if (response.data.exito) {
          setHistorial(response.data.historial);

          // Inicializamos el estado expandedItems para que todos los elementos estén acortados por defecto
          const expandedDefault = response.data.historial.reduce(
            (acc, _, index) => {
              acc[index] = false; // Marca todos los elementos como no expandidos
              return acc;
            },
            {}
          );
          setExpandedItems(expandedDefault);
        } else {
          setError("Error al cargar el historial.");
        }
      } catch (err) {
        console.error(err);
        setError(err.message || "Error al obtener el historial.");
      } finally {
        setLoading(false); // Terminar la carga
      }
    };

    obtenerHistorial();
  }, []);

  const toggleExpand = (index) => {
    setExpandedItems((prevState) => ({
      ...prevState,
      [index]: !prevState[index],
    }));
  };

  const generarPDF = () => {
    const doc = new jsPDF();
    let y = 10; // Coordenada inicial para el contenido

    doc.setFont("helvetica", "bold");
    doc.setFontSize(16);
    doc.text("Informe de Resultados", 10, y);
    y += 10;

    historial.forEach((item, index) => {
      doc.setFont("helvetica", "bold");
      doc.setFontSize(14);
      doc.text(`Elemento #${index + 1}`, 10, y);
      y += 10;

      // Función para agregar datos al PDF, incluyendo estructuras anidadas
      const agregarDatos = (obj, indent = 0) => {
        Object.entries(obj).forEach(([key, value]) => {
          const texto =
            typeof value === "object" && value !== null
              ? `${key}:` // Para objetos anidados, solo muestra la clave
              : `${key}: ${value || "N/A"}`; // Valor directo

          doc.setFont("helvetica", "normal");
          doc.setFontSize(12);

          const indentX = 10 + indent * 5; // Indentación
          const lineas = doc.splitTextToSize(texto, 180 - indentX); // Ajustar al ancho disponible

          // Agregar nuevas páginas si es necesario
          if (y + lineas.length * 10 > 280) {
            doc.addPage();
            y = 10;
          }
          doc.text(lineas, indentX, y);
          y += lineas.length * 10;

          // Si el valor es un objeto, llamar recursivamente
          if (typeof value === "object" && value !== null) {
            agregarDatos(value, indent + 1);
          }
        });
      };

      // Llamada a la función con el objeto actual
      agregarDatos(item);

      y += 5; // Espacio extra entre elementos
    });

    doc.save("informe_resultados.pdf"); // Guardar el PDF
  };

  return (
    <div
      style={{
        maxWidth: "800px",
        margin: "0 auto",
        padding: "3px",
      }}
    >
      <h1>Informe de Resultados</h1>

      {loading ? ( // Mostrar indicador de carga mientras loading es true
        <p>Cargando datos...</p>
      ) : error ? ( // Mostrar error si ocurre
        <p style={{ color: "red" }}>{error}</p>
      ) : (
        <>
        <div style={{textAlign:"end"}}>
          <button
            onClick={generarPDF}
            style={{
              padding: "10px 20px",
              backgroundColor: "#007BFF",
              color: "#fff",
              border: "none",
              borderRadius: "5px",
              cursor: "pointer",
              marginBottom: "20px",
            }}
          >
            Descargar PDF
          </button>
        </div>

          {historial.length > 0 ? (
            <div style={{ display: "grid", gap: "10px" }}>
              {historial.map((item, index) => (
                <div
                  key={index}
                  style={{
                    border: "1px solid #ccc",
                    borderRadius: "8px",
                    padding: "15px",
                    backgroundColor: "#e6f7ff",
                    position: "relative",
                  }}
                >
                  <div
                    style={{
                      whiteSpace: expandedItems[index] ? "pre-wrap" : "nowrap",
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                    }}
                  >
                    <pre
                   style={{
                    width: '97%', // Ajusta el ancho relativo al contenedor padre
                    maxWidth: '800px', // Establece un ancho máximo absoluto para pantallas grandes
                    background: '#f5f5f5',
                    padding: '1em',
                    borderRadius: '5px',
                    margin: '0 auto', // Centra horizontalmente el contenedor
                  }}
                  
                    >
                      {JSON.stringify(item, null, 2)}
                    </pre>
                  </div>
                  <button
                    onClick={() => toggleExpand(index)}
                    style={{
                      position: "absolute",
                      top: "10px",
                      right: "10px",
                      border: "none",
                      background: "none",
                      color: "#007BFF",
                      cursor: "pointer",
                      fontSize: "16px",
                      fontWeight: "bold",
                    }}
                  >
                    {expandedItems[index] ? "-" : "+"}
                  </button>
                </div>
              ))}
            </div>
          ) : (
            <p>No hay datos en el historial.</p>
          )}
        </>
      )}
    </div>
  );
}

export default Informe;
