import React from "react";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import clienteAxios from "../../config/axios";

function Plan({ proyecto, onDelete }) {
    const {
        id_plan, id_proyecto,
        nombre, descripcion,
        id_responsable, responsable_nombre, // responsable_nombre es el nombre relacionado al idUsuario
        fecha_creacion, fecha_ejecucion, estado,
    } = proyecto;

    // Función para eliminar proyecto
    const eliminarProyecto = (idProyecto) => {
        Swal.fire({
            title: "¿Estás seguro?",
            text: "Se eliminará el registro",
            icon: "warning",
            showCancelButton: true,
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            confirmButtonText: "Sí, eliminar",
            cancelButtonText: "Cancelar",
        }).then((result) => {
            if (result.isConfirmed) {
                // Llamada a Axios para eliminar
                clienteAxios.delete(`/plan/${idProyecto}`)
                    .then((res) => {
                        console.log(res);
                        onDelete(); // Llama a onDelete para recargar la lista
                        Swal.fire("Eliminado!", "El proyecto ha sido eliminado.", "success");
                    })
                    .catch((error) => {
                        console.error("Error al eliminar el proyecto:", error);
                        Swal.fire("Error", "No se pudo eliminar el proyecto", "error");
                    });
            }
        });
    };

    const formatDate = (dateString) => {
        if (!dateString) return '';
        const options = { year: 'numeric', month: '2-digit', day: '2-digit' };
        return new Date(dateString).toLocaleDateString('es-GT', options);
    };

    return (
        <li className="cliente">
            <div className="info-cliente">
                <p className="nombre"><strong>Código Plan:</strong> {id_plan}</p>
                <p className="empresa"><strong>Nombre:</strong> {nombre}</p>
                <p><strong>Descripción:</strong> {descripcion}</p>
                <p><strong>Código Proyecto:</strong> {id_proyecto}</p>
                <p><strong>Responsable:</strong> {responsable_nombre || id_responsable}</p>
                <p><strong>Fecha de Creación:</strong> {formatDate(fecha_creacion)}</p>
                <p><strong>Fecha Ejecución:</strong> {formatDate(fecha_ejecucion)}</p>
                <p><strong>Estado:</strong> {estado}</p>
            </div>
            <div className="acciones">
                <Link to={"/productos/nuevo"} className="btn btn-verde nvo-cliente">
                    <i className="fas fa-plus-circle"></i> Nuevo Plan
                </Link>
                <Link to={`/productos/editar/${id_plan}`} className="btn btn-azul">
                    <i className="fas fa-pen-alt"></i> Editar Plan
                </Link>
                <button
                    type="button"
                    className="btn btn-rojo btn-eliminar"
                    onClick={() => eliminarProyecto(id_plan)}
                >
                    <i className="fas fa-times"></i> Eliminar Plan
                </button>
            </div>
        </li>
    );
}

export default Plan;
