import React, { Fragment, useState, useEffect } from "react";
import clienteAxios from "../../config/axios";
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';

// Componente principal que permite agregar un nuevo defecto
function NuevoDefecto() {
    const navigate = useNavigate();

    // Estado para los datos del defecto a agregar
    const [proyecto, guardaProyectos] = useState({
        nombre: '',
        descripcion: '',
        id_proyecto: '',
        prioridad: '',
        responsable: '', // Este campo ahora almacenará el nombre completo del usuario
        estado: '',
        fecha_actualizacion: '',
        evidencia: ''
    });

    // Estado para almacenar los proyectos obtenidos desde el backend
    const [proyectos, setProyectos] = useState([]);

    // Estado para almacenar los usuarios obtenidos desde el backend
    const [usuarios, setUsuarios] = useState([]);

    // Efecto para cargar los proyectos y usuarios al montar el componente
    useEffect(() => {
        // Obtener proyectos desde la API
        clienteAxios.get('/proyecto')
            .then(res => {
                setProyectos(res.data); // Guardar proyectos en el estado
            })
            .catch(error => {
                console.error("Error al obtener proyectos:", error.response?.data || error.message);
            });

        // Obtener usuarios desde la API
        clienteAxios.get('/usuarios')
            .then(res => {
                setUsuarios(res.data); // Guardar usuarios en el estado
            })
            .catch(error => {
                console.error("Error al obtener usuarios:", error.response?.data || error.message);
            });
    }, []);

    // Manejar cambios en los campos del formulario
    const actualizarState = e => {
        const { name, value } = e.target; // Desestructuración del evento
        guardaProyectos({
            ...proyecto, // Mantener el resto de los valores del estado
            [name]: value // Actualizar solo el campo modificado
        });
    };

    // Manejar el envío del formulario
    const agregarProyecto = e => {
        e.preventDefault(); // Evitar recargar la página

        // Enviar los datos al backend
        clienteAxios.post('/defecto', proyecto, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(res => {
            // Mostrar mensaje de éxito
            Swal.fire(
                "Se Agregó el defecto",
                `Código del nuevo defecto: ${res.data.id}`,
                "success"
            );
            // Redirigir a la lista de defectos
            navigate('/defectos');
        })
        .catch(error => {
            // Manejar errores
            console.error("Error al agregar el defecto:", error.response?.data || error.message);
        });
    };

    // Limpiar los campos del formulario
    const limpiarFormulario = () => {
        guardaProyectos({
            nombre: '',
            descripcion: '',
            id_proyecto: '',
            prioridad: '',
            responsable: '',
            estado: '',
            fecha_actualizacion: '',
            evidencia: ''
        });
    };

    // Validar que no haya campos requeridos vacíos
    const validarProyecto = () => {
        const { nombre, descripcion, id_proyecto, prioridad, responsable, estado, evidencia } = proyecto;
        return !nombre || !descripcion || !id_proyecto || !prioridad || !responsable || !estado || !evidencia;
    };

    return (
        <Fragment>
            <h2>Nuevo Defecto</h2>
            <form onSubmit={agregarProyecto}>
                <legend>Llena todos los campos</legend>

                {/* Campo para el nombre del defecto */}
                <div className="campo">
                    <label>Nombre del defecto:</label>
                    <input 
                        type="text" 
                        placeholder="Ingrese nombre" 
                        name="nombre" 
                        value={proyecto.nombre} 
                        onChange={actualizarState} 
                    />
                </div>

                {/* Campo para la descripción */}
                <div className="campo">
                    <label>Descripción:</label>
                    <input 
                        type="text" 
                        placeholder="Ingrese descripción" 
                        name="descripcion" 
                        value={proyecto.descripcion} 
                        onChange={actualizarState} 
                    />
                </div>

                {/* Campo de selección para el proyecto */}
                <div className="campo">
                    <label htmlFor="id_proyecto">Selecciona un Proyecto:</label>
                    <select 
                        name="id_proyecto" 
                        id="id_proyecto" 
                        value={proyecto.id_proyecto} 
                        onChange={actualizarState} 
                        required
                    >
                        <option value="">Seleccione un proyecto</option>
                        {proyectos.map(proj => (
                            <option key={proj.id_proyecto} value={proj.id_proyecto}>
                                {`ID: ${proj.id_proyecto} - ${proj.nombre}`}
                            </option>
                        ))}
                    </select>
                </div>

                {/* Campo de prioridad */}
                <div className="campo">
                    <label htmlFor="prioridad">Prioridad:</label>
                    <select 
                        name="prioridad" 
                        id="prioridad" 
                        value={proyecto.prioridad} 
                        required 
                        onChange={actualizarState}
                    >
                        <option value="">Seleccione Prioridad</option>
                        <option value="critico">Crítico</option>
                        <option value="alto">Alto</option>
                        <option value="medio">Medio</option>
                        <option value="bajo">Bajo</option>
                    </select>
                </div>

                {/* Campo de selección para el responsable */}
                <div className="campo">
                    <label htmlFor="responsable">Selecciona un Responsable:</label>
                    <select 
                        name="responsable" 
                        id="responsable" 
                        value={proyecto.responsable} 
                        onChange={actualizarState} 
                        required
                    >
                        <option value="">Seleccione un responsable</option>
                        {usuarios.map(usuario => (
                            <option key={usuario.idUsuario} value={`${usuario.nombre} ${usuario.apellido}`}>
                                {`${usuario.nombre} ${usuario.apellido}`}
                            </option>
                        ))}
                    </select>
                </div>

                {/* Campo de estado */}
                <div className="campo">
                    <label htmlFor="estado">Estado:</label>
                    <select 
                        name="estado" 
                        id="estado" 
                        value={proyecto.estado} 
                        required 
                        onChange={actualizarState}
                    >
                        <option value="">Seleccione un estado</option>
                        <option value="nuevo">Nuevo</option>
                        <option value="proceso">Proceso</option>
                        <option value="corregido">Corregido</option>
                        <option value="verificado">Verificado</option>
                    </select>
                </div>

                {/* Campo para evidencia */}
                <div className="campo">
                    <label>Evidencia:</label>
                    <input 
                        type="text" 
                        placeholder="Ingrese evidencia" 
                        name="evidencia" 
                        value={proyecto.evidencia} 
                        onChange={actualizarState} 
                    />
                </div>

                {/* Botones de acción */}
                <div style={{ display: 'flex', justifyContent: 'flex-start', marginTop: '20px' }}>
                    <input 
                        type="submit" 
                        className="btn btn-azul" 
                        value="Agregar Defecto" 
                        disabled={validarProyecto()} 
                        style={{ padding: '10px 20px', marginRight: '10px' }} 
                    />
                    <button 
                        type="button" 
                        onClick={limpiarFormulario} 
                        style={{ padding: '10px 20px', backgroundColor: '#dc3545', color: 'white', border: 'none', borderRadius: '5px', cursor: 'pointer' }}
                    >
                        Limpiar Campos
                    </button>
                </div>
            </form>
        </Fragment>
    );
}

// Exportar el componente
export default NuevoDefecto;
