import React, { Fragment, useState } from "react";
import clienteAxios from "../../config/axios";
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';

// Componente principal que permite agregar un nuevo usuario
function NuevoUsuario() {
    // Hook para redirigir al usuario después de agregar el nuevo usuario
    const navigate = useNavigate();

    // Estado para almacenar los datos del nuevo usuario
    const [proyecto, guardaProyectos] = useState({
        nombre: '', apellido: '', password: '', usuario: '', correo: '', rol: '', estado: ''
    });

    // Función para actualizar el estado cuando el usuario escribe en los campos
    const actualizarState = e => {
        guardaProyectos({
            ...proyecto,
            [e.target.name]: e.target.value
        });
    };

    // Función para agregar un nuevo usuario (enviar datos al backend)
    const agregarProyecto = e => {
        e.preventDefault(); // Evita el comportamiento por defecto del formulario (recargar página)

        // Realiza la solicitud al backend para registrar el nuevo usuario
        clienteAxios.post('/usuario/registrar', proyecto, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(res => {
            // Si el usuario se agrega correctamente, se muestra un mensaje de éxito
            Swal.fire(
                "Se Agregó el Usuario",
                `Código del nuevo usuario: ${res.data.idUsuario}`, // Muestra el código del nuevo usuario
                "success"
            );
            // Redirige a la página de usuarios
            navigate('/usuarios');
        })
        .catch(error => {
            // En caso de error, se maneja la respuesta del backend y muestra un mensaje adecuado

            // Verifica si el error tiene una respuesta específica del backend
            console.error("Error al agregar el usuario:", error.response?.data || error.message);

            // Si el error contiene un mensaje de backend, se muestra en el modal
            if (error.response && error.response.data && error.response.data.mensaje) {
                Swal.fire({
                    icon: "error", // Icono de error
                    title: "Error al Registrar Usuario", // Título del modal
                    text: error.response.data.mensaje, // Muestra el mensaje del backend
                });
            } else {
                // Si no se recibe un mensaje específico, se muestra un mensaje genérico
                Swal.fire({
                    icon: "error", // Icono de error
                    title: "Error Desconocido", // Título del modal
                    text: "Hubo un problema al registrar el usuario. Intenta nuevamente.", // Mensaje genérico
                });
            }
        });
    };

    // Función para limpiar el formulario
    const limpiarFormulario = () => {
        guardaProyectos({
            nombre: '', apellido: '', password: '', usuario: '', correo: '', rol: '', estado: ''
        });
    };

    // Función para validar que todos los campos estén completos antes de enviar el formulario
    const validarProyecto = () => {
        const { nombre, apellido, password, usuario, correo, rol, estado } = proyecto;
        // Retorna 'true' si falta algún campo, lo que deshabilita el botón de envío
        return !nombre || !apellido || !password || !usuario || !correo || !rol || !estado;
    };

    return (
        <Fragment>
            <h2>Nuevo Usuario</h2>
            <form onSubmit={agregarProyecto}>
                <legend>Llena todos los campos</legend>

                {/* Campos del formulario */}
                <div className="campo">
                    <label>Nombre:</label>
                    <input
                        type="text"
                        placeholder="Ingrese nombre"
                        name="nombre"
                        value={proyecto.nombre}
                        onChange={actualizarState}
                    />
                </div>
                <div className="campo">
                    <label>Apellido:</label>
                    <input
                        type="text"
                        placeholder="Ingrese apellido"
                        name="apellido"
                        value={proyecto.apellido}
                        onChange={actualizarState}
                    />
                </div>
                <div className="campo">
                    <label>Usuario:</label>
                    <input
                        type="text"
                        placeholder="Ingrese usuario"
                        name="usuario"
                        value={proyecto.usuario}
                        onChange={actualizarState}
                    />
                </div>
                <div className="campo">
                    <label>Password:</label>
                    <input
                        type="password"
                        placeholder="Ingrese password"
                        name="password"
                        value={proyecto.password}
                        onChange={actualizarState}
                    />
                </div>
                <div className="campo">
                    <label>Correo Eléctronico:</label>
                    <input
                        type="text"
                        placeholder="Ingrese correo"
                        name="correo"
                        value={proyecto.correo}
                        onChange={actualizarState}
                    />
                </div>
                <div className="campo">
                    <label htmlFor="rol">Rol:</label>
                    <select
                        name="rol"
                        id="rol"
                        value={proyecto.rol}
                        required
                        onChange={actualizarState}
                    >
                        <option value="">Seleccione un Rol</option>
                        <option value="admin">Admin</option>
                        <option value="test">Test</option>
                        <option value="desarrollador">Desarrollador</option>
                    </select>
                </div>
                <div className="campo">
                    <label htmlFor="estado">Estado:</label>
                    <select
                        name="estado"
                        id="estado"
                        value={proyecto.estado}
                        required
                        onChange={actualizarState}
                    >
                        <option value="">Seleccione un estado</option>
                        <option value="A">Activo</option>
                        <option value="I">Inactivo</option>
                    </select>
                </div>

                {/* Contenedor para los botones con estilos en línea */}
                <div style={{ display: 'flex', justifyContent: 'flex-start', marginTop: '20px' }}>
                    {/* Botón para enviar el formulario */}
                    <input
                        type="submit"
                        className="btn btn-azul"
                        value="Agregar Usuario"
                        disabled={validarProyecto()} // Deshabilita el botón si falta algún campo
                        style={{ padding: '10px 20px', marginRight: '10px' }}
                    />
                    {/* Botón para limpiar el formulario */}
                    <button
                        type="button"
                        onClick={limpiarFormulario}
                        style={{
                            padding: '10px 20px',
                            backgroundColor: '#dc3545',
                            color: 'white',
                            border: 'none',
                            borderRadius: '5px',
                            cursor: 'pointer',
                        }}
                    >
                        Limpiar Campos
                    </button>
                </div>
            </form>
        </Fragment>
    );
}

// Exportar el componente
export default NuevoUsuario;
