import React, { Fragment, useState } from "react";
import clienteAxios from "../../config/axios";
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';

// Componente principal que permite agregar un nuevo proyecto
function NuevoProyecto() {
    const navigate = useNavigate();
    const [proyecto, guardaProyectos] = useState({
        nombre: '',
        descripcion: '',
        fecha_inicio: '',
        fecha_fin_estimada: '',
        estado: '',
        recursos: '',
        presupuesto_estimado: ''
    });

    const actualizarState = e => {
        guardaProyectos({
            ...proyecto,
            [e.target.name]: e.target.value
        });
    };

    const agregarProyecto = e => {
        e.preventDefault();
        clienteAxios.post('/proyecto', proyecto, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(res => {
            Swal.fire(
                "Se Agregó el Proyecto",
                `Código del nuevo proyecto: ${res.data.id}`,
                "success"
            );
            navigate('/clientes');
        })
        .catch(error => {
            console.error("Error al agregar el proyecto:", error.response?.data || error.message);
        });
    };

    const limpiarFormulario = () => {
        guardaProyectos({
            nombre: '',
            descripcion: '',
            fecha_inicio: '',
            fecha_fin_estimada: '',
            estado: '',
            recursos: '',
            presupuesto_estimado: ''
        });
    };

    const validarProyecto = () => {
        const { nombre, descripcion, fecha_inicio, fecha_fin_estimada, estado, recursos, presupuesto_estimado } = proyecto;
        return !nombre || !descripcion || !fecha_inicio || !fecha_fin_estimada || !estado || !recursos || !presupuesto_estimado;
    };

    return (
        <Fragment>
            <h2>Nuevo Proyecto</h2>
            <form onSubmit={agregarProyecto}>
                <legend>Llena todos los campos</legend>

                {/* Campos del formulario */}
                <div className="campo">
                    <label>Nombre del Proyecto:</label>
                    <input type="text" placeholder="Nombre del Proyecto" name="nombre" value={proyecto.nombre} onChange={actualizarState} />
                </div>
                <div className="campo">
                    <label>Descripción:</label>
                    <input type="text" placeholder="Descripción" name="descripcion" value={proyecto.descripcion} onChange={actualizarState} />
                </div>
                <div className="campo">
                    <label htmlFor="fecha_inicio">Fecha de Inicio:</label>
                    <input type="date" name="fecha_inicio" id="fecha_inicio" value={proyecto.fecha_inicio} onChange={actualizarState} required />
                </div>
                <div className="campo">
                    <label htmlFor="fecha_fin_estimada">Fecha Fin Estimada:</label>
                    <input type="date" name="fecha_fin_estimada" id="fecha_fin_estimada" value={proyecto.fecha_fin_estimada} onChange={actualizarState} required />
                </div>
                <div className="campo">
                    <label htmlFor="estado">Estado:</label>
                    <select name="estado" id="estado" value={proyecto.estado} required onChange={actualizarState}>
                        <option value="">Seleccione un estado</option>
                        <option value="A">Activo</option>
                        <option value="P">Progreso</option>
                        <option value="F">Finalizado</option>
                        <option value="C">Cancelado</option>
                    </select>
                </div>
                <div className="campo">
                    <label>Recursos:</label>
                    <input type="text" placeholder="Recursos" name="recursos" value={proyecto.recursos} onChange={actualizarState} />
                </div>
                <div className="campo">
                    <label>Presupuesto Estimado:</label>
                    <input type="text" placeholder="Presupuesto Estimado" name="presupuesto_estimado" value={proyecto.presupuesto_estimado} onChange={actualizarState} />
                </div>

                {/* Contenedor para los botones con estilos en línea */}
                <div style={{ display: 'flex', justifyContent: 'flex-start', marginTop: '20px' }}>
                    <input type="submit" className="btn btn-azul" value="Agregar Proyecto" disabled={validarProyecto()} style={{ padding: '10px 20px', marginRight: '10px' }} />
                    <button type="button" onClick={limpiarFormulario} style={{ padding: '10px 20px', backgroundColor: '#dc3545', color: 'white', border: 'none', borderRadius: '5px', cursor: 'pointer' }}>
                        Limpiar Campos
                    </button>
                </div>
            </form>
        </Fragment>
    );
}

// Exportar el componente
export default NuevoProyecto;
