import React from "react";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import clienteAxios from "../../config/axios";

function CasoPrueba({ proyecto, onDelete }) {
    const {
        id_caso, id_escenario,
        descripcion, criterios_aceptacion
    } = proyecto;

    // Función para eliminar proyecto
    const eliminarProyecto = (id_caso) => {
        Swal.fire({
            title: "¿Estás seguro?",
            text: "Se eliminará el registro",
            icon: "warning",
            showCancelButton: true,
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            confirmButtonText: "Sí, eliminar",
            cancelButtonText: "Cancelar",
        }).then((result) => {
            if (result.isConfirmed) {
                // Llamada a Axios para eliminar
                clienteAxios.delete(`/caso/${id_caso}`)
                    .then((res) => {
                        console.log(res);
                        onDelete(); // Llama a onDelete para recargar la lista
                        Swal.fire("Eliminado!", "El proyecto ha sido eliminado.", "success");
                    })
                    .catch((error) => {
                        console.error("Error al eliminar el proyecto:", error);
                        Swal.fire("Error", "No se pudo eliminar el proyecto", "error");
                    });
            }
        });
    };

    return (
        <li className="cliente">
            <div className="info-cliente">
                <p className="nombre"><strong>Código Caso Prueba:</strong> {id_caso}</p>
                <p className="empresa"><strong>Código Escenario:</strong> {id_escenario}</p>
                <p><strong>Descripción:</strong> {descripcion}</p>
                <p><strong>Criterios de Aceptación:</strong> {criterios_aceptacion}</p>
            </div>
            <div className="acciones">
                {/* Asegúrate de que estas rutas estén correctamente configuradas en tu sistema de rutas */}
                <Link to={"/casos/nuevo"} className="btn btn-verde nvo-cliente">
                    <i className="fas fa-plus-circle"></i> Nuevo Caso
                </Link>
                <Link to={`/casos/editar/${id_caso}`} className="btn btn-azul">
                    <i className="fas fa-pen-alt"></i> Editar Caso
                </Link>
                <button
                    type="button"
                    className="btn btn-rojo btn-eliminar"
                    onClick={() => eliminarProyecto(id_caso)}
                >
                    <i className="fas fa-times"></i> Eliminar Caso
                </button>
            </div>
        </li>
    );
}

export default CasoPrueba;
