import React, { Fragment, useEffect, useState } from "react";
import { useParams, useNavigate } from 'react-router-dom';
import clienteAxios from "../../config/axios"; // Configuración de Axios para hacer solicitudes HTTP
import Swal from 'sweetalert2'; // Librería para mostrar alertas

// Componente principal para editar un proyecto existente
function EditarDefecto() {
    // Extrae el ID del proyecto desde los parámetros de la URL
    const { id_defecto } = useParams(); 
    const navigate = useNavigate(); // Hook para la navegación después de actualizar
    const [proyecto, guardaProyectos] = useState({
        nombre: '',
        descripcion: '',
        id_proyecto: '',
        prioridad: '',
        responsable: '', // Este campo ahora almacenará el nombre completo del usuario
        estado: '',
        evidencia: ''
    });

    // Función para consultar el proyecto en la API usando su ID
    const consultarAPI = async () => {
        try {
            const proyectoConsulta = await clienteAxios.get(`/defecto/${id_defecto}`);
            const proyectoData = proyectoConsulta.data;

            // Actualiza el estado con las fechas formateadas
            guardaProyectos({
                ...proyectoData
            });
        } catch (error) {
            console.error("Error al obtener el defecto:", error);
            Swal.fire("Error", "El defecto no se pudo encontrar o no existe", "error"); // Mensaje más claro para el usuario
        }
    };

    // useEffect para ejecutar la consulta al cargar el componente
    useEffect(() => {
        consultarAPI();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [id_defecto]); // La dependencia es el ID del proyecto

    // Actualiza el estado del proyecto en base a los datos del formulario
    const actualizarState = e => {
        guardaProyectos({
            ...proyecto,
            [e.target.name]: e.target.value // Cambia solo el valor del campo modificado
        });
    };

    // Limpia los campos del formulario
    const limpiarFormulario = () => {
        guardaProyectos({
            nombre: '',
            descripcion: '',
            id_proyecto: '',
            prioridad: '',
            responsable: '', // Este campo ahora almacenará el nombre completo del usuario
            estado: '',
            evidencia: ''
        });
    };

    // Valida si todos los campos están llenos
    const validarProyecto = () => {
        const { nombre, descripcion, id_proyecto, prioridad, responsable, estado, evidencia } = proyecto;
        // Retorna true si algún campo está vacío
        return !nombre || !descripcion || !id_proyecto || !prioridad || !responsable || !estado || !evidencia;
    };

  
// Función para enviar la solicitud de actualización a la API
const agregarProyecto = async (e) => {
    e.preventDefault(); // Previene el comportamiento por defecto del formulario

    try {
        // Realiza una solicitud PUT para actualizar el proyecto en la API sin almacenar el resultado
        await clienteAxios.put(`/defecto/${id_defecto}`, proyecto);
        
        // Muestra un mensaje de éxito
        Swal.fire("Se Actualizó el defecto", `Código del defecto actualizado: ${id_defecto}`, "success");
        navigate('/defectos'); // Redirige a la página principal después de la actualización
    } catch (error) {
        console.error("Error al actualizar el defecto:", error); // Muestra el error en consola si falla
        Swal.fire("Error", "No se pudo actualizar el defecto", "error"); // Muestra una alerta de error
    }
};


    return (
        <Fragment>
            <h2>Editar Defecto</h2>
            <form onSubmit={agregarProyecto}>
                <legend>Llena todos los campos</legend>

                {/* Campo para el nombre del defecto */}
                <div className="campo">
                    <label>Nombre del defecto:</label>
                    <input 
                        type="text" 
                        placeholder="Ingrese nombre" 
                        name="nombre" 
                        value={proyecto.nombre} 
                        onChange={actualizarState} 
                    />
                </div>

                {/* Campo para la descripción */}
                <div className="campo">
                    <label>Descripción:</label>
                    <input 
                        type="text" 
                        placeholder="Ingrese descripción" 
                        name="descripcion" 
                        value={proyecto.descripcion} 
                        onChange={actualizarState} 
                    />
                </div>

                {/* Campo de selección para el proyecto */}
                <div className="campo">
                    <label>Código Proyecto:</label>
                    <input 
                        type="text" 
                        placeholder="Ingrese proyecto" 
                        name="id_proyecto" 
                        value={proyecto.id_proyecto} 
                        onChange={actualizarState}
                        disabled 
                    />
                </div>

                {/* Campo de prioridad */}
                <div className="campo">
                    <label htmlFor="prioridad">Prioridad:</label>
                    <select 
                        name="prioridad" 
                        id="prioridad" 
                        value={proyecto.prioridad} 
                        required 
                        onChange={actualizarState}
                    >
                        <option value="">Seleccione Prioridad</option>
                        <option value="critico">Crítico</option>
                        <option value="alto">Alto</option>
                        <option value="medio">Medio</option>
                        <option value="bajo">Bajo</option>
                    </select>
                </div>

                {/* Campo de selección para el responsable */}
                <div className="campo">
                    <label>Responsable:</label>
                    <input 
                        type="text" 
                        placeholder="Ingrese responsable" 
                        name="responsable" 
                        value={proyecto.responsable} 
                        onChange={actualizarState}
                        disabled 
                    />
                </div>

                {/* Campo de estado */}
                <div className="campo">
                    <label htmlFor="estado">Estado:</label>
                    <select 
                        name="estado" 
                        id="estado" 
                        value={proyecto.estado} 
                        required 
                        onChange={actualizarState}
                    >
                        <option value="">Seleccione un estado</option>
                        <option value="nuevo">Nuevo</option>
                        <option value="proceso">Proceso</option>
                        <option value="corregido">Corregido</option>
                        <option value="verificado">Verificado</option>
                    </select>
                </div>

                {/* Campo para evidencia */}
                <div className="campo">
                    <label>Evidencia:</label>
                    <input 
                        type="text" 
                        placeholder="Ingrese evidencia" 
                        name="evidencia" 
                        value={proyecto.evidencia} 
                        onChange={actualizarState} 
                    />
                </div>

                {/* Contenedor de botones */}
                <div style={{ display: 'flex', justifyContent: 'flex-start', marginTop: '20px' }}>
                    {/* Botón para actualizar el proyecto */}
                    <input type="submit" className="btn btn-azul" value="Actualizar Defecto" disabled={validarProyecto()} style={{ padding: '10px 20px', marginRight: '10px' }} />
                    {/* Botón para limpiar el formulario */}
                    <button type="button" onClick={limpiarFormulario} style={{ padding: '10px 20px', backgroundColor: '#dc3545', color: 'white', border: 'none', borderRadius: '5px', cursor: 'pointer' }}>
                        Limpiar Campos
                    </button>
                </div>
            </form>
        </Fragment>
    );
}

// Exporta el componente para usarlo en otras partes de la aplicación
export default EditarDefecto;
