import React, { Fragment, useState, useEffect } from "react";
import clienteAxios from "../../config/axios";
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';

// Componente principal que permite agregar un nuevo escenario
function NuevoEscenario() {
    const navigate = useNavigate();
    const [proyecto, guardaProyectos] = useState({
        id_plan_prueba: '',
        nombre: '',
        descripcion: '',
    });
    const [planesPrueba, setPlanesPrueba] = useState([]); // Estado para almacenar los planes de prueba

    // Cargar los planes de prueba cuando el componente se monta
    useEffect(() => {
        clienteAxios.get('/plan') // Se asume que la ruta '/plan' devuelve los planes de prueba
            .then(res => {
                setPlanesPrueba(res.data); // Guardar los planes de prueba en el estado
            })
            .catch(error => {
                console.error("Error al obtener planes de prueba:", error.response?.data || error.message);
            });
    }, []);

    const actualizarState = e => {
        guardaProyectos({
            ...proyecto,
            [e.target.name]: e.target.value
        });
    };

    const agregarProyecto = e => {
        e.preventDefault();
        clienteAxios.post('/escenario', proyecto, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(res => {
            Swal.fire(
                "Se Agregó el Escenario",
                `Código del nuevo escenario: ${res.data.id}`,
                "success"
            );
            navigate('/escenarios');
        })
        .catch(error => {
            console.error("Error al agregar el escenario:", error.response?.data || error.message);
        });
    };

    const limpiarFormulario = () => {
        guardaProyectos({
            id_plan_prueba: '',
            nombre: '',
            descripcion: ''
        });
    };

    const validarProyecto = () => {
        const { id_plan_prueba, nombre, descripcion } = proyecto;
        return !id_plan_prueba || !nombre || !descripcion;
    };

    return (
        <Fragment>
            <h2>Nuevo Escenario</h2>
            <form onSubmit={agregarProyecto}>
                <legend>Llena todos los campos</legend>

                {/* Campo para seleccionar el Plan de Prueba */}
                <div className="campo">
                    <label>Código Plan Prueba:</label>
                    <select
                        name="id_plan_prueba"
                        value={proyecto.id_plan_prueba}
                        onChange={actualizarState}
                        required
                    >
                        <option value="">Seleccione un Plan de Prueba</option>
                        {planesPrueba.map(plan => (
                            <option key={plan.id_plan} value={plan.id_plan}>
                                {`ID: ${plan.id_plan} - ${plan.nombre}`}
                            </option>
                        ))}
                    </select>
                </div>

                {/* Campos del formulario */}
                <div className="campo">
                    <label>Nombre del Escenario:</label>
                    <input
                        type="text"
                        placeholder="Nombre del Escenario"
                        name="nombre"
                        value={proyecto.nombre}
                        onChange={actualizarState}
                    />
                </div>
                <div className="campo">
                    <label>Descripción:</label>
                    <input
                        type="text"
                        placeholder="Descripción"
                        name="descripcion"
                        value={proyecto.descripcion}
                        onChange={actualizarState}
                    />
                </div>

                {/* Contenedor para los botones con estilos en línea */}
                <div style={{ display: 'flex', justifyContent: 'flex-start', marginTop: '20px' }}>
                    <input
                        type="submit"
                        className="btn btn-azul"
                        value="Agregar Escenario"
                        disabled={validarProyecto()}
                        style={{ padding: '10px 20px', marginRight: '10px' }}
                    />
                    <button
                        type="button"
                        onClick={limpiarFormulario}
                        style={{ padding: '10px 20px', backgroundColor: '#dc3545', color: 'white', border: 'none', borderRadius: '5px', cursor: 'pointer' }}
                    >
                        Limpiar Campos
                    </button>
                </div>
            </form>
        </Fragment>
    );
}

// Exportar el componente
export default NuevoEscenario;
