import React, { Fragment, useEffect, useState } from "react";
import { jsPDF } from "jspdf";
import clienteAxios from "../../config/axios";

function NuevaPrueba() {
  const [archivo, setArchivo] = useState(null);
  const [dragging, setDragging] = useState(false);
  const [resultado, setResultado] = useState(null);
  const [loading, setLoading] = useState(false); // Nuevo estado para indicar carga
  const [error, setError] = useState(null);
  const [caso, setCaso] = useState(null);
  const [idCasoSeleccionado, setIdCasoSeleccionado] = useState("");

  useEffect(() => {
    const obtenerCaso = async () => {
      try {
        const response = await clienteAxios.get("/caso/");
        console.log("Caso de prueba:", response.data);
        if (response.data) {
          setCaso(response.data);
        } else {
          setError("Error al cargar el caso de prueba.");
        }
      } catch (err) {
        console.error(err);
        setError(error.message || "Error al obtener el caso de prueba.");
      }
    };
    obtenerCaso();
  }, []);

  const manejarCambioArchivo = (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const tiposPermitidos = ["application/json", "text/plain"];
    if (!tiposPermitidos.includes(file.type)) {
      alert("Solo se permiten archivos .json o .txt.");
      e.target.value = null;
      return;
    }
    setArchivo(file);
  };

  const manejarArchivoDragDrop = (e) => {
    e.preventDefault();
    e.stopPropagation();
    const file = e.dataTransfer.files[0];
    setArchivo(file);
    setDragging(false);
  };

  const manejarDragOver = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setDragging(true);
  };

  const manejarDragLeave = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setDragging(false);
  };

  const manejarEnvioArchivo = async (e) => {
    e.preventDefault();

    if (!archivo) {
      alert("Por favor, selecciona un archivo antes de enviarlo.");
      return;
    }

    const formData = new FormData();
    formData.append("id_caso", idCasoSeleccionado);
    formData.append("archivo", archivo);

    setLoading(true); // Inicia la carga
    setError(null); // Resetea cualquier error previo

    try {
      // Enviar el archivo
      const response = await clienteAxios.post(
        `/ejecucion/subir-y-ejecutar`,
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        }
      );

      if (response.status === 200) {
        console.log("Datos del archivo:", response.data);
        setResultado(response.data);
      } else {
        setError("Error al intentar enviar el archivo.");
      }
    } catch (error) {
      console.log("Error al enviar el archivo:", error.response.data);
      const mensajeError =
        error.response?.data || "Error al enviar el archivo.";
      setError(mensajeError); // Guardar el mensaje de error en la variable
    } finally {
      // Actualizar historial

      setLoading(false); // Finaliza la carga
    }
  };

  const manejarCambioCaso = (e) => {
    setIdCasoSeleccionado(e.target.value);
  };

  const dividirTexto = (doc, texto, maxWidth) => {
    return doc.splitTextToSize(texto, maxWidth);
  };

  const generarPDF = () => {
    const doc = new jsPDF();

    // Título del PDF
    doc.setFontSize(18);
    doc.text("Reporte de Resultados", 10, 10);

    let y = 20; // Coordenada inicial en Y

    // Verifica si hay resultados para mostrar
    if (resultado && resultado.exito === false) {
      // Mostrar el mensaje de error del resultado
      doc.setFontSize(12);
      const mensajeError = resultado.error || "Error desconocido.";
      doc.text(`Error: ${mensajeError}`, 10, y);
    } else if (resultado && Array.isArray(resultado.resultados)) {
      resultado.resultados.forEach((item, index) => {
        doc.setFont("helvetica", "bold");
        doc.setFontSize(14);
        doc.text(`Resultado ${index + 1}`, 10, y);
        y += 10;

        // URL del resultado
        const urlDividida = dividirTexto(doc, item.url || "No disponible", 180);
        doc.setFont("helvetica", "normal");
        doc.setFontSize(12);
        doc.text(`URL:`, 10, y);
        y += 10;
        urlDividida.forEach((linea) => {
          doc.text(linea, 15, y);
          y += 10;
        });

        // id del caso
        doc.text(`ID del caso: ${item.id_caso || "No disponible"}`, 10, y);
        y += 10;

        doc.text(`Estado: ${item.status || "No disponible"}`, 10, y);
        y += 10;
        doc.text(`Tiempo: ${item.tiempo || "No disponible"} ms`, 10, y);
        y += 10;

        // Detalles específicos según los datos
        if (item.datos) {
          if (item.datos.metadata) {
            // Datos específicos de metadata
            const metadata = item.datos.metadata;
            doc.text(`Título: ${metadata.title || "No disponible"}`, 10, y);
            y += 10;
            doc.text(`Conteo: ${metadata.count || "No disponible"}`, 10, y);
            y += 10;
          } else if (item.datos.categories) {
            // Datos específicos para categorías de chistes
            doc.text(
              `Categorías: ${
                Array.isArray(item.datos.categories)
                  ? item.datos.categories.join(", ")
                  : "No disponible"
              }`,
              10,
              y
            );
            y += 10;
            doc.text(`Valor: ${item.datos.value || "No disponible"}`, 10, y);
            y += 10;
          } else if (item.datos.info) {
            // Información específica de usuarios aleatorios
            doc.text(
              `Página: ${item.datos.info.page || "No disponible"}`,
              10,
              y
            );
            y += 10;
          }
        }

        // Línea separadora entre resultados
        doc.line(10, y, 200, y);
        y += 10;

        // Salto de página si necesario
        if (y > 280) {
          doc.addPage();
          y = 10;
        }
      });
    } else {
      // Caso en que no hay resultados o `resultado` no es válido
      doc.setFontSize(12);
      doc.text(
        `Error: ${
          typeof error === "object"
            ? JSON.stringify(error, null, 2)
            : error || "No se encontraron resultados"
        }`,
        10,
        y
      );
    }

    // Guardar el PDF
    doc.save("reporte_resultados.pdf");
  };

  const handleReset = () => {
    setArchivo(null);
    setResultado(null);
    setError(null);
  }

  return (
    <div
      style={{
        maxWidth: "800px", // Define el ancho máximo
        margin: "0 auto", // Centra el contenido horizontalmente
        padding: "20px", // Opcional: añade espacio interno
      }}
    >
      <h2>Nueva Prueba</h2>

      <form onSubmit={(e) => e.preventDefault()}>
        {caso && caso.length > 0 ? (
          <div>
            <label htmlFor="caso" style={{fontWeight:"bold"}}>Selecciona un caso de prueba:</label>
            &nbsp;
            <select
              className="select-estilizado"
              value={idCasoSeleccionado}
              onChange={manejarCambioCaso}
            >
              <option value="">Selecciona un caso de prueba</option>
              {caso.map((item) => (
                <option key={item.id_caso} value={item.id_caso}>
                  {item.descripcion}
                </option>
              ))}
            </select>
          </div>
        ) : (
          <p>No hay casos disponibles.</p>
        )}

        <div
          style={{
            marginTop: "20px",
            padding: "20px",
            border: "2px dashed #ccc",
            borderRadius: "10px",
            backgroundColor: dragging ? "#f0f8ff" : "#fafafa",
            textAlign: "center",
            cursor: "pointer",
          }}
          onDragOver={manejarDragOver}
          onDragEnter={manejarDragOver}
          onDragLeave={manejarDragLeave}
          onDrop={manejarArchivoDragDrop}
          onClick={() => document.getElementById("file-upload").click()} // Simula el clic en el input
        >
          <p style={{ fontSize: "16px", margin: "10px 0" }}>
            {archivo ? (
              <strong>{archivo.name}</strong>
            ) : (
              <>
                <i className="fas fa-upload" style={{ fontSize: "24px" }}></i>
                <br />
                <span>Arrastra tu archivo aquí o haz clic para subirlo</span>
              </>
            )}
          </p>
          <input
            type="file"
            accept=".json,.txt"
            style={{ display: "none" }}
            id="file-upload"
            onChange={manejarCambioArchivo}
          />
        </div>

        {archivo && (
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              marginTop: "20px",
            }}
          >
            <button
              type="button"
              className="btn btn-verde"
              onClick={manejarEnvioArchivo}
            >
              Subir Archivo
            </button>
            &nbsp;
            <button
              type="button"
              className="btn btn-azul"
              onClick={handleReset}
            >
              {" "}
              Resetear{" "}
            </button>
          </div>
        )}
      </form>

      {loading && (
        <div
          style={{
            margin: "20px auto",
            textAlign: "center",
          }}
        >
          <div className="spinner"></div>
          <p>Cargando...</p>
        </div>
      )}

      {resultado && (
        <div style={{ marginTop: "20px" }}>
          <h3>Resultado de la ejecución</h3>
          <p>Resultado exitoso más Información en el pdf</p>
          <button
            style={{
              marginTop: "10px",
              padding: "10px 20px",
              backgroundColor: "#28a745",
              color: "#fff",
              borderRadius: "5px",
              cursor: "pointer",
            }}
            onClick={generarPDF}
          >
            Descargar PDF
          </button>
        </div>
      )}

      {error && (
        <div style={{ marginTop: "20px" }}>
          <h3>Resultado de la ejecución</h3>
          <pre>{JSON.stringify(error, null, 2)}</pre>
          <button
            style={{
              marginTop: "10px",
              padding: "10px 20px",
              backgroundColor: "#28a745",
              color: "#fff",
              borderRadius: "5px",
              cursor: "pointer",
            }}
            onClick={generarPDF}
          >
            Descargar PDF
          </button>
        </div>
      )}
    </div>
  );
}

export default NuevaPrueba;
