import React, { Fragment, useState, useEffect } from "react";
import clienteAxios from "../../config/axios";
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';

// Componente principal que permite agregar un nuevo plan
function NuevoPlan() {
    const navigate = useNavigate();
    const [proyecto, guardaProyectos] = useState({
        id_proyecto: '', // Campo para almacenar el id del proyecto seleccionado
        nombre: '',
        descripcion: '',
        fecha_ejecucion: '',
        estado: '',
        id_responsable: '' // Nuevo campo para almacenar el id del responsable
    });
    const [proyectos, setProyectos] = useState([]); // Estado para almacenar los proyectos obtenidos
    const [usuarios, setUsuarios] = useState([]); // Estado para almacenar los usuarios

    // Efecto para cargar los proyectos cuando el componente se monta
    useEffect(() => {
        clienteAxios.get('/proyecto') // Ruta de los proyectos
            .then(res => {
                setProyectos(res.data); // Guardar los proyectos en el estado
            })
            .catch(error => {
                console.error("Error al obtener proyectos:", error.response?.data || error.message);
            });

        // Efecto para cargar los usuarios cuando el componente se monta
        clienteAxios.get('/usuarios') // Ruta de los usuarios
            .then(res => {
                setUsuarios(res.data); // Guardar los usuarios en el estado
            })
            .catch(error => {
                console.error("Error al obtener usuarios:", error.response?.data || error.message);
            });
    }, []);

    // Actualizar el estado de proyecto al cambiar los campos del formulario
    const actualizarState = e => {
        guardaProyectos({
            ...proyecto,
            [e.target.name]: e.target.value
        });
    };

    // Enviar el formulario para agregar un nuevo plan
    const agregarProyecto = e => {
        e.preventDefault();
        clienteAxios.post('/plan', proyecto, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(res => {
            Swal.fire(
                "Se Agregó el Plan",
                `Código del nuevo plan: ${res.data.id}`,
                "success"
            );
            navigate('/productos');
        })
        .catch(error => {
            console.error("Error al agregar el proyecto:", error.response?.data || error.message);
        });
    };

    // Limpiar los campos del formulario
    const limpiarFormulario = () => {
        guardaProyectos({
            id_proyecto: '',
            nombre: '',
            descripcion: '',
            id_responsable: '',
            fecha_creacion: '',
            fecha_ejecucion: '',
            estado: ''
        });
    };

    // Validar si falta algún campo requerido para el formulario
    const validarProyecto = () => {
        const { nombre, descripcion, fecha_ejecucion, estado, id_proyecto, id_responsable } = proyecto;
        return !nombre || !descripcion || !fecha_ejecucion || !estado || !id_proyecto || !id_responsable;
    };

    return (
        <Fragment>
            <h2>Nuevo Plan</h2>
            <form onSubmit={agregarProyecto}>
                <legend>Llena todos los campos</legend>
                {/* Campos del formulario */}
                <div className="campo">
                    <label>Nombre del Plan:</label>
                    <input 
                        type="text" 
                        placeholder="Nombre del Plan" 
                        name="nombre" 
                        value={proyecto.nombre} 
                        onChange={actualizarState} 
                    />
                </div>
                <div className="campo">
                    <label>Descripción:</label>
                    <input 
                        type="text" 
                        placeholder="Descripción" 
                        name="descripcion" 
                        value={proyecto.descripcion} 
                        onChange={actualizarState} 
                    />
                </div>
                <div className="campo">
                    <label htmlFor="fecha_ejecucion">Fecha de Ejecución:</label>
                    <input 
                        type="date" 
                        name="fecha_ejecucion" 
                        id="fecha_ejecucion" 
                        value={proyecto.fecha_ejecucion} 
                        onChange={actualizarState} 
                        required 
                    />
                </div>

                                {/* Campo de selección para el proyecto */}
                                <div className="campo">
                    <label htmlFor="id_proyecto">Selecciona un Proyecto:</label>
                    <select 
                        name="id_proyecto" 
                        id="id_proyecto" 
                        value={proyecto.id_proyecto} 
                        onChange={actualizarState} 
                        required
                    >
                        <option value="">Seleccione un proyecto</option>
                        {proyectos.map(proj => (
                            <option key={proj.id_proyecto} value={proj.id_proyecto}>
                                {`ID: ${proj.id_proyecto} - ${proj.nombre}`}
                            </option>
                        ))}
                    </select>
                </div>

                {/* Campo de selección para el responsable */}
                <div className="campo">
                    <label htmlFor="id_responsable">Selecciona un Responsable:</label>
                    <select 
                        name="id_responsable" 
                        id="id_responsable" 
                        value={proyecto.id_responsable} 
                        onChange={actualizarState} 
                        required
                    >
                        <option value="">Seleccione un responsable</option>
                        {usuarios.map(usuario => (
                            <option key={usuario.idUsuario} value={usuario.idUsuario}>
                                {`${usuario.nombre} ${usuario.apellido}`} {/* Asumiendo que los usuarios tienen nombre y apellido */}
                            </option>
                        ))}
                    </select>
                </div>
                
                <div className="campo">
                    <label htmlFor="estado">Estado:</label>
                    <select 
                        name="estado" 
                        id="estado" 
                        value={proyecto.estado} 
                        required 
                        onChange={actualizarState}
                    >
                        <option value="">Seleccione un estado</option>
                        <option value="activo">Activo</option>
                        <option value="proceso">Progreso</option>
                        <option value="finalizado">Finalizado</option>
                        <option value="cancelado">Cancelado</option>
                    </select>
                </div>

                {/* Contenedor para los botones */}
                <div style={{ display: 'flex', justifyContent: 'flex-start', marginTop: '20px' }}>
                    <input 
                        type="submit" 
                        className="btn btn-azul" 
                        value="Agregar Plan" 
                        disabled={validarProyecto()} 
                        style={{ padding: '10px 20px', marginRight: '10px' }} 
                    />
                    <button 
                        type="button" 
                        onClick={limpiarFormulario} 
                        style={{ padding: '10px 20px', backgroundColor: '#dc3545', color: 'white', border: 'none', borderRadius: '5px', cursor: 'pointer' }}
                    >
                        Limpiar Campos
                    </button>
                </div>
            </form>
        </Fragment>
    );
}

// Exportar el componente
export default NuevoPlan;
